import { Injectable } from '@angular/core';
import { StateService } from '../common/state-service.abstract';
import { BehaviorSubject, Observable } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';
import { UserMenuItem } from './user.model';
import { sortByPriority } from '../common';

@Injectable({
  providedIn: 'root'
})
export class UserMenuService extends StateService {
  state$ = new BehaviorSubject({
    userMenuItems: new Set<UserMenuItem>()
  });
  items$: Observable<UserMenuItem[]>;

  constructor() {
    super();
    this.items$ = this.state$.pipe(
      map(state => Array.from(state.userMenuItems.values())),
      map(items => sortByPriority(items)),
      distinctUntilChanged()
    );
  }

  /**
   * Returns the current state.
   * @readonly
   * @returns The current set of actions.
   */
  get state() {
    return this.state$.value;
  }

  /**
   * Adds a new item to the search in the header.
   * @param item The item to add.
   */
  add(item: UserMenuItem) {
    this.state.userMenuItems.add(item);
    this.emitNewState();
  }

  /**
   * Removes an search from the header.
   * @param item The item to remove.
   */
  remove(item: UserMenuItem) {
    this.state.userMenuItems.delete(item);
    this.emitNewState();
  }
}
