import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { filter, map, switchMap } from 'rxjs/operators';
import { OptionsService } from '../common/options.service';
import { Permissions } from '../common/permissions.service';
import { AppStateService } from '../common/ui-state.service';
import { Steppers } from '../stepper/stepper.model';
import { StepperService } from '../stepper/stepper.service';
import { NEEDED_ROLE_FOR_SETUP, SetupStep, SETUP_FINISHED_STEP_ID } from './setup.model';
import { PluginsResolveService } from '../plugins/plugins-resolve.service';

@Injectable({
  providedIn: 'root'
})
export class SetupService {
  /**
   * Indicates if the applications needs to show a setup wizard.
   */
  isSetupNeeded$: Observable<boolean>;

  private skipSetup$ = new BehaviorSubject(false);

  /**
   * @ignore only DI and initialization
   */
  constructor(
    private stepperService: StepperService,
    private permissions: Permissions,
    private options: OptionsService,
    public ui: AppStateService,
    private pluginsResolve: PluginsResolveService
  ) {
    this.isSetupNeeded$ = this.pluginsResolve.allPluginsLoaded$.pipe(
      filter(loaded => loaded),
      switchMap(() => this.ui.isOwnerOfApplication$()),
      switchMap(isOwner => {
        if (!isOwner) {
          return of([]);
        }
        return this.stepperService.getById$(Steppers.SETUP);
      }),
      map((steps: SetupStep[]) => this.getNotCompletedSetupSteps(steps)),
      map(
        notCompletedSetupSteps =>
          this.options.forceSetup ||
          (this.options.isSetup && this.needsSetup(notCompletedSetupSteps))
      ),
      switchMap(required => this.setupRequired(required))
    );
  }

  skipSetup() {
    this.skipSetup$.next(true);
  }

  private setupRequired(required: boolean): Observable<boolean> {
    if (!required) {
      return of(required);
    }
    return this.skipSetup$.pipe(map(skipped => !skipped));
  }

  private needsSetup(notCompletedSetupSteps: SetupStep[]): boolean {
    notCompletedSetupSteps = this.excludeSetupFinishStep(notCompletedSetupSteps);
    if (this.hasPermission()) {
      return notCompletedSetupSteps.length > 0;
    }
    // Show setup screen as there are required steps but the user
    // has no permission. The Setup screen will show an error to
    // the user.
    return this.hasRequiredSteps(notCompletedSetupSteps);
  }

  private excludeSetupFinishStep(notCompletedSetupSteps: SetupStep[]) {
    return notCompletedSetupSteps.filter(({ setupId }) => setupId !== SETUP_FINISHED_STEP_ID);
  }

  private hasRequiredSteps(notCompletedSetupSteps: SetupStep[]): boolean {
    return notCompletedSetupSteps.some(step => step.required);
  }

  private hasPermission(): boolean {
    return this.permissions.hasRole(NEEDED_ROLE_FOR_SETUP);
  }

  private getNotCompletedSetupSteps(steps: SetupStep[]): SetupStep[] {
    if (!this.ui.currentApplication.value) {
      return steps;
    }
    return steps.filter(
      step =>
        step.setupId &&
        !(this.ui.currentApplication.value.config?.setup || []).includes(step.setupId)
    );
  }
}
