import { Injectable } from '@angular/core';
import { BsModalRef, BsModalService, ModalOptions } from 'ngx-bootstrap/modal';
import { WizardComponent } from './wizard.component';

@Injectable({
  providedIn: 'root'
})
export class WizardModalService {
  constructor(private bsModalService: BsModalService) {}
  /**
   * Shows a wizard modal.
   * @param modalOptions Modal options.
   * @returns a reference to the modal.
   */
  show(modalOptions: ModalOptions = {}): BsModalRef<WizardComponent> {
    const options = {
      class: 'modal-sm',
      ariaDescribedby: 'modal-body',
      ariaLabelledBy: 'modal-title',
      backdrop: 'static',
      ...modalOptions
    } as ModalOptions<WizardComponent>;
    return this.bsModalService.show<WizardComponent>(WizardComponent, options);
  }
}
