import { IResult, Service } from '@c8y/client';
import { pick } from 'lodash-es';
import { Observable } from 'rxjs';
import { DynamicBulkRetrievalError } from './dynamic-bulk-retrieval-error.model';
import { DynamicComponentAlert } from './dynamic-component-alert.model';
import { DynamicBulkDetailsResolver } from './dynamic-details-resolver';
import { DynamicResolverService } from './dynamic-resolver.service';

export abstract class DynamicBulkIIdentifiedResolver<
  T extends { id: string | number; [key: string]: any }
> extends DynamicBulkDetailsResolver<T> {
  /**
   * Used within the buildRetrievalAlert method to generate the alert text.
   * For ManagedObjects this would be set to managedObject, for events e.g. to event.
   */
  protected abstract readonly typeForErrorMessage: string;

  constructor(
    protected dynamicResolver: DynamicResolverService,
    /**
     * You either have to provide the service or implement the performBulkRequest method on your own.
     */
    protected service?: Service<T> & { detail(entityOrId: string | number): Promise<IResult<T>> }
  ) {
    super(dynamicResolver);
  }

  buildRetrievalAlert(
    entity: Partial<T>,
    errors: DynamicBulkRetrievalError[]
  ): DynamicComponentAlert {
    const moError = `"${entity.name || entity.text || '-'}" (${entity.id})`;

    let details = '';
    let foundErrorForId: DynamicBulkRetrievalError;
    if (errors?.length) {
      foundErrorForId = errors.find(tmp => tmp.id === entity.id);
      if (foundErrorForId) {
        details = `. ${foundErrorForId.status}: ${foundErrorForId.statusText}`;
      }
    }

    const alert = new DynamicComponentAlert({
      text: `Unable to retrieve the following ${this.typeForErrorMessage}: ${moError}${details}`,
      type: 'danger',
      unresolvedData: entity
    });
    alert.retrievalError = foundErrorForId;
    return alert;
  }

  protected performBulkRequest(
    uniqIds: string[],
    bulkRequestId: number
  ):
    | Promise<{ result: T[]; bulkRequestId: number; errors: DynamicBulkRetrievalError[] }>
    | Observable<{ result: T[]; bulkRequestId: number; errors: DynamicBulkRetrievalError[] }> {
    if (!this.service) {
      console.error(
        `Either provide a service to the DynamicBulkIIdentifiedResolver constructor or implement performBulkRequest yourself.`
      );
      return Promise.resolve({ bulkRequestId, errors: [], result: [] });
    }

    const promArray: Array<Promise<IResult<T> | DynamicBulkRetrievalError>> = uniqIds.map(id =>
      this.service.detail(id).catch(res => ({ id, ...pick(res?.res, ['status', 'statusText']) }))
    );
    return Promise.all(promArray).then(result => {
      const successfulResults = result
        .map((tmp: IResult<T>) => tmp.data)
        .filter(tmp => !!tmp) as T[];
      const errors = result.filter(
        (tmp: DynamicBulkRetrievalError) => !!tmp.id
      ) as DynamicBulkRetrievalError[];
      return { bulkRequestId, errors, result: successfulResults };
    });
  }

  protected extractIdsToBeRetrieved(
    valueBehindAttribute: Partial<T> | Array<Partial<T>>
  ): string | string[] {
    if (Array.isArray(valueBehindAttribute)) {
      return valueBehindAttribute
        .map(entry => entry?.id)
        .filter(tmp => !!tmp)
        .map(tmp => `${tmp}`);
    }

    const id = valueBehindAttribute?.id;
    if (id) {
      return `${id}`;
    }
    return null;
  }

  protected serializeSingleObject(obj: T): Partial<T> {
    if (!obj) {
      return obj;
    }
    return pick(obj, ['id', 'name', 'text']) as Partial<T>;
  }
}
