import {
  AfterViewInit,
  Component,
  ComponentRef,
  EnvironmentInjector,
  EventEmitter,
  Injector,
  Input,
  OnDestroy,
  Output,
  Type,
  ViewChild,
  ViewContainerRef
} from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { WizardEntry } from '../wizard.model';
import { WizardService } from '../wizard.service';

@Component({
  selector: 'c8y-wizard-outlet',
  templateUrl: './wizard-outlet.component.html'
})
export class WizardOutletComponent implements AfterViewInit, OnDestroy {
  @ViewChild('container', { read: ViewContainerRef }) container: ViewContainerRef;
  /**
   * The initial state to be assigned to the created component.
   */
  @Input() initialState: any = {};
  /**
   * The id parameter identifies the particular wizard and can be used to refer to it when hooking wizard entries.
   * Example: "wizardId".
   */
  @Input() id: string;
  /**
   * The event that is emitted when the wizard entry is clicked.
   */
  @Output() onSelect: EventEmitter<WizardEntry> = new EventEmitter();
  /**
   * The event that is emitted when the URL path is provided.
   */
  @Output() onPath: EventEmitter<string> = new EventEmitter();
  /**
   * List of wizard entries.
   */
  entries: WizardEntry[] = [];
  /**
   * @ignore
   */
  showList = true;
  /**
   * @ignore
   */
  private destroy$: Subject<boolean> = new Subject<boolean>();
  /**
   * @ignore
   */
  private componentInstance: ComponentRef<unknown>;
  constructor(
    private router: Router,
    private c8yWizardService: WizardService,
    private viewContainerRef: ViewContainerRef,
    private injector: Injector
  ) {}

  ngAfterViewInit() {
    this.c8yWizardService
      .getEntriesListById(this.id)
      .pipe(takeUntil(this.destroy$))
      .subscribe((entries: WizardEntry[]) => {
        this.container.clear();
        this.entries = entries;
        this.onEntriesLoaded();
      });
  }

  /**
   * Handles the wizard entry, if the entry contains a path, the user will be redirected to it.
   * Otherwise, an attempt will be made to create a dynamic component provided in the entry.
   * @param entry Wizard menu entry.
   */
  handleEntry(entry: WizardEntry = {} as WizardEntry) {
    const { path, component, injector } = entry;
    if (path) {
      this.handlePath(path);
      return;
    }

    if (component) {
      this.handleComponent(component, injector);
      this.onSelect.emit(entry);
    }
  }

  /**
   * Resets the wizard to its initial state.
   */
  reset() {
    if (this.componentInstance) {
      this.componentInstance = undefined;
      this.container.clear();
      this.showList = true;
    }
  }

  ngOnDestroy() {
    if (this.componentInstance) {
      this.container.clear();
    }
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }

  private onEntriesLoaded() {
    const [firstEntry] = this.entries;
    const isSingleEntry = this.entries.length === 1;
    const shouldRedirectToPath = firstEntry && !!firstEntry.path;
    const shouldOmitListOfEntries = isSingleEntry && !shouldRedirectToPath;

    if (shouldOmitListOfEntries) {
      this.handleEntry(firstEntry);
    }
  }

  private handlePath(path: string) {
    this.router.navigateByUrl(path);
    this.onPath.emit(path);
  }

  private handleComponent(component: Type<unknown>, injector: Injector) {
    this.showList = false;
    const componentInstance = this.viewContainerRef.createComponent(component, {
      environmentInjector: (injector || this.injector) as EnvironmentInjector
    });
    Object.assign(componentInstance.instance, this.initialState);
    this.componentInstance = componentInstance;
  }
}
