import { Component, OnDestroy, OnInit } from '@angular/core';
import { ApplicationRemotePlugins } from '@c8y/client';
import { Observable, Subject, combineLatest, of } from 'rxjs';
import { distinctUntilChanged, map, startWith, switchMap, takeUntil } from 'rxjs/operators';
import { ActionBarService } from '../action-bar/action-bar.service';
import { OptionsService } from '../common/options.service';
import { AppStateService } from '../common/ui-state.service';
import { HeaderService } from '../header/header.service';
import { NavigatorService } from '../navigator/navigator.service';
import { PluginsResolveService } from '../plugins/plugins-resolve.service';
import { PluginsConfig } from '../plugins/plugins.model';
import { SetupService } from '../setup/setup.service';
import { TabsService } from '../tabs/tabs.service';
import { isEmpty } from 'lodash-es';
import { LoginService } from '../login/login.service';

@Component({
  selector: 'c8y-bootstrap',
  templateUrl: './bootstrap.template.html'
})
export class BootstrapComponent implements OnDestroy, OnInit {
  navigatorOpen$: Observable<boolean>;
  noAppsMargin$: Observable<boolean>;
  tabsOrientation: string;
  showPoweredBy = true;
  showLogin$: Observable<boolean>;
  showLoadingIndicator$: Observable<boolean>;
  showMainView$: Observable<boolean>;
  showSetup$: Observable<boolean>;
  private destroy$: Subject<boolean> = new Subject<boolean>();
  private loadedRemotesContextPathCache = new Map<string, boolean>();

  constructor(
    public tabs: TabsService,
    public ui: AppStateService,
    public navigator: NavigatorService,
    public actionBar: ActionBarService,
    public headerService: HeaderService,
    private options: OptionsService,
    private pluginsResolve: PluginsResolveService,
    public setupService: SetupService,
    public loginService: LoginService
  ) {
    this.noAppsMargin$ = this.headerService.map(({ nav }) => !nav.open && nav.hiddenOnStartup);
    this.tabsOrientation = this.options.tabsHorizontal ? 'horizontal' : 'vertical';
    this.ui
      .map(({ lang }) => lang)
      .pipe(takeUntil(this.destroy$), distinctUntilChanged())
      .subscribe(() => {
        this.actionBar.refresh();
      });
    this.showPoweredBy = !this.options.get('hidePowered');
    const noLogin$ = of(this.options.get<boolean>('noLogin', false));
    this.showLogin$ = combineLatest([noLogin$, this.ui.currentUser]).pipe(
      map(([noLogin, currentUser]) => !noLogin && !currentUser)
    );
    this.showLoadingIndicator$ = combineLatest([
      noLogin$,
      this.ui.currentUser,
      this.loginService.automaticLoginInProgress$,
      this.pluginsResolve.allPluginsLoaded$
    ]).pipe(
      map(
        ([noLogin, currentUser, automaticLoginInProgress, allPluginsLoaded]) =>
          (!noLogin && automaticLoginInProgress) || (currentUser && !allPluginsLoaded)
      ),
      startWith(true)
    );
    this.showMainView$ = noLogin$.pipe(
      switchMap(noLogin => {
        if (noLogin) {
          return of(true);
        }
        return combineLatest([
          this.ui.currentUser,
          this.setupService.isSetupNeeded$,
          this.pluginsResolve.allPluginsLoaded$
        ]).pipe(
          map(([currentUser, isSetupNeeded, allPluginsLoaded]) => {
            return currentUser && !isSetupNeeded && allPluginsLoaded;
          })
        );
      })
    );
    this.showSetup$ = combineLatest([this.ui.currentUser, this.setupService.isSetupNeeded$]).pipe(
      map(([currentUser, isSetupNeeded]) => currentUser && isSetupNeeded)
    );
  }

  async ngOnInit() {
    this.subscribeToLoadRemotes();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private getRemotes(config: PluginsConfig): ApplicationRemotePlugins {
    let remotes: ApplicationRemotePlugins;
    if (!!config?.remotes) {
      // Merge plugins config remotes and remotes passed in the URL.
      // Remotes in the URL are used in the development process.
      remotes = this.pluginsResolve.mergeMFRemotes([
        config.remotes,
        this.pluginsResolve.loadUrlRemotes()
      ]);
    } else {
      remotes = this.options?.remotes;
    }

    // Block the possibility of loading plugins multiple times.
    Object.keys(remotes || {}).forEach(contextPath => {
      if (this.loadedRemotesContextPathCache.get(contextPath)) {
        delete remotes[contextPath];
      }
    });
    return isEmpty(remotes) ? undefined : remotes;
  }

  private async loadRemotes(remotes: ApplicationRemotePlugins) {
    if (!remotes) {
      this.ensureWeMarkPluginsAsLoaded();
      return;
    }
    const remoteModules = await this.pluginsResolve.loadRemotes(remotes);

    // Cache loaded remotes.
    Object.keys(remotes).forEach(contextPath =>
      this.loadedRemotesContextPathCache.set(contextPath, true)
    );

    if (remoteModules.length) {
      this.pluginsResolve.resolveRemotePlugins(remoteModules);
    }
  }

  private subscribeToLoadRemotes(): void {
    if (this.options.get('noPlugins', false)) {
      this.ensureWeMarkPluginsAsLoaded();
      return;
    }

    if (this.options.get<boolean>('noLogin', false)) {
      this.loadRemotes(this.getRemotes(null));
      return;
    }

    this.ui.currentApplicationConfig
      .pipe(takeUntil(this.destroy$))
      .subscribe((config: PluginsConfig) => this.loadRemotes(this.getRemotes(config)));
  }

  private ensureWeMarkPluginsAsLoaded(): void {
    // ensure we call markPluginsAsLoaded on the DynamicComponentService
    this.pluginsResolve.markPluginsAsLoaded();
  }
}
