import { Inject, Injectable, Optional } from '@angular/core';
import { Observable, of } from 'rxjs';
import { distinctUntilChanged, filter, map, shareReplay } from 'rxjs/operators';
import { AppStateService, ExtensionFactory } from '../common';
import { gettext } from '../i18n';
import { Version, VersionModuleConfig, VERSION_MODULE_CONFIG } from './version.model';

/**
 * Will provide the backend versions of the tenant.
 * The system option 'system' -> 'version' is used for this.
 */
@Injectable({
  providedIn: 'root'
})
export class BackendVersionFactory implements ExtensionFactory<Version> {
  backendVersion$: Observable<Version | Version[]> = of([]);
  constructor(
    @Optional() @Inject(VERSION_MODULE_CONFIG) private config: VersionModuleConfig,
    private appState: AppStateService
  ) {
    if (this.config?.disableBackendVersionFactory) {
      return;
    }
    this.backendVersion$ = this.appState.state$.pipe(
      map(state => state?.versions?.backend),
      filter(backendVersion => !!backendVersion),
      distinctUntilChanged(),
      map(backendVersion => this.buildBackendVersion(backendVersion)),
      shareReplay(1)
    );
  }

  get(): Observable<Version | Version[]> {
    return this.backendVersion$;
  }

  private buildBackendVersion(backendVersion: string): Version {
    return { label: gettext('Backend'), version: backendVersion, priority: 20, type: 'SYSTEM' };
  }
}
