import {
  AfterContentInit,
  Component,
  ContentChild,
  DoCheck,
  HostBinding,
  Input,
  OnDestroy
} from '@angular/core';
import { FormControl, FormControlName, NgModel, ValidationErrors } from '@angular/forms';
import { BehaviorSubject, merge, Subject } from 'rxjs';
import { debounceTime, filter, takeUntil } from 'rxjs/operators';
import { MessagesComponent } from './messages.component';

/**
 * A form group helps to validate an input of a form element.
 *
 * ## Example:
 *
 * ```html
 *  <c8y-form-group [hasWarning]="user.email.length === 0">
 *   <label translate for="userEmail">Email</label>
 *   <input
 *     id="userEmail"
 *     class="form-control"
 *     type="email"
 *     name="email"
 *     [maxlength]="254"
 *     autocomplete="off"
 *     placeholder="{{'e.g. joe.doe@example.com' | translate}}"
 *     [(ngModel)]="user.email"
 *     email
 *     required
 *   >
 *   <c8y-messages>
 *     <c8y-message *ngIf="user.email.length === 0" translate></c8y-message>
 *     <c8y-message name="required" text="The E-Mail is SUPER required"></c8y-message>
 *   </c8y-messages>
 *  </c8y-form-group>
 * ```
 *
 * @param status The current status could be error, warning or success.
 * @param hasError Set this to true to display a error.
 * @param hasWarning Set this to true to display a warning.
 * @param hasSuccess Set this to true to display a success.
 * @param novalidation Set this to true to disable automatic validation by this component.
 */
@Component({
  selector: 'c8y-form-group',
  templateUrl: './form-group.component.html',
  host: {
    class: 'form-group'
  }
})
export class FormGroupComponent implements AfterContentInit, DoCheck, OnDestroy {
  @Input() hasError = false;
  @Input() hasWarning = false;
  @Input() hasSuccess = false;
  @Input() novalidation = false;
  @Input() status: string;

  @HostBinding('class.has-error') get error() {
    return this.status === 'error' || this.hasError;
  }
  @HostBinding('class.has-warning') get warning() {
    return this.status === 'warning' || this.hasWarning;
  }
  @HostBinding('class.has-success') get success() {
    return this.status === 'success' || this.hasSuccess;
  }

  @ContentChild(MessagesComponent, { static: false }) customErrorMessage: MessagesComponent;
  @ContentChild(NgModel, { static: true }) model: NgModel;
  @ContentChild(FormControlName, { static: true }) formControlName: FormControlName;

  errors: ValidationErrors;

  private control: FormControl | NgModel;
  private controlTouched$: BehaviorSubject<boolean>;

  private readonly VALIDATION_DEBOUNCE_MS = 100;

  private destroyed$: Subject<void> = new Subject();

  ngAfterContentInit(): void {
    this.initNgModel();
    this.initFormControl();
  }

  initNgModel() {
    if (this.model) {
      this.observeControl(this.model);
    }
  }

  initFormControl() {
    if (this.formControlName) {
      this.observeControl(this.formControlName.control);
    }
  }

  observeControl(control: FormControl | NgModel) {
    this.control = control;
    this.controlTouched$ = new BehaviorSubject<boolean>(control.touched);
    merge(control.valueChanges, control.statusChanges, this.controlTouched$)
      .pipe(
        filter(() => (control.dirty || control.touched) && !this.novalidation),
        debounceTime(this.VALIDATION_DEBOUNCE_MS),
        takeUntil(this.destroyed$)
      )
      .subscribe(() => this.updateErrors(control));
  }

  updateErrors(control: FormControl | NgModel) {
    this.hasError = control.touched && control.status === 'INVALID';
    const errors = this.hasError ? control.errors : {};
    if (this.customErrorMessage) {
      this.customErrorMessage.changeVisibility(errors);
    } else {
      this.errors = errors;
    }
  }

  ngDoCheck() {
    if (this.controlTouched$) {
      if (this.controlTouched$.value !== this.control.touched) {
        this.controlTouched$.next(this.control.touched);
      }
    }
  }

  ngOnDestroy() {
    this.destroyed$.next();
    this.destroyed$.complete();
  }
}
