import {
  Component,
  Input,
  ViewChild,
  ElementRef,
  AfterViewInit,
  HostListener,
  OnInit,
  forwardRef,
  Inject,
  Injector,
  Output,
  EventEmitter
} from '@angular/core';
import { fromEvent, merge, Observable, of } from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  map,
  sample,
  switchMap,
  tap,
  withLatestFrom
} from 'rxjs/operators';
import * as mimeDB from 'mime-db';
import { FilesService, IFetchWithProgress } from '../common';
import {
  AbstractControl,
  ControlValueAccessor,
  FormControl,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  NgControl,
  ValidationErrors,
  Validator
} from '@angular/forms';
import { FilePickerValidators } from './file-picker-validators';
import { validateArrayElements } from '../forms';
import { gettext } from '../i18n';

interface IAvailableValidators {
  fileMaxSize?: boolean;
  fileNonEmpty?: boolean;
  fileNameMaxLength?: boolean;
}

/**
 File uploader
 */

@Component({
  selector: 'c8y-file-picker-form-control',
  templateUrl: './file-picker-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => FilePickerFormControlComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: forwardRef(() => FilePickerFormControlComponent)
    }
  ]
})
export class FilePickerFormControlComponent
  implements OnInit, AfterViewInit, ControlValueAccessor, Validator
{
  @ViewChild('filePlaceholder', { static: true })
  filePlaceholder: ElementRef<HTMLElement>;

  /** Specifies a filter for what file types the user can pick from the file input dialog box.
   * By default all types are accepted.
   * ## Example:
   *
   * Specify file types by extensions:
   * ```html
   *  ...
   *  [accept]="'.zip,.7z'"
   *  ...
   * ```
   *
   * Specify file types by extensions and generic types [GENERIC_FILE_TYPE]{@link GENERIC_FILE_TYPE}:
   * ```html
   *  ...
   *  [accept]="'.pdf,archive'"
   *  ...
   * ```
   *
   * Specify file types by generic types [GENERIC_FILE_TYPE]{@link GENERIC_FILE_TYPE}:
   *  ```html
   *  ...
   *  [accept]="'archive,video'"
   *  ...
   *
   * ```
   */
  @Input()
  accept?: string;
  @Input()
  maxAllowedFiles?: number = Infinity;
  @Input()
  minRequiredFiles?: number = 1;
  @Input()
  disableValidators: IAvailableValidators = {};
  @Input()
  uploadInProgress = false;
  /** The array of progress state objects for each selected file (matched by index). */
  @Input()
  uploadProgress: IFetchWithProgress[];
  /** Display warning if any file dragged over the drop area has unsupported extension. */
  @Input()
  validateExtensionOnDrag = false;
  @Output()
  dropped: EventEmitter<File[]> = new EventEmitter();

  droppedFiles: File[] = [];

  isDraggingFiles$: Observable<boolean>;
  isDraggingOverFilePlaceholder$: Observable<boolean>;
  isExtensionAllowed$: Observable<boolean>;
  isDropAreaEnlarged$: Observable<boolean>;
  isDraggedFileUnsupported$: Observable<boolean>;
  acceptedExts: string[];
  valid: boolean;
  errors: ValidationErrors;
  controlInstance: AbstractControl;
  // TODO handle control disabled state
  disabled: boolean;
  disabledValidators: IAvailableValidators = {
    fileMaxSize: false,
    fileNonEmpty: false,
    fileNameMaxLength: false
  };
  loadingMessage = gettext('Upload in progress');
  private isExtensionAllowed: boolean;
  private fileSizeLimit: number;

  constructor(private filesService: FilesService, @Inject(Injector) private injector: Injector) {}

  ngOnInit(): void {
    const ngControl = this.injector.get<NgControl | false>(NgControl, false);

    if (ngControl && ngControl.control) {
      this.controlInstance = ngControl.control;
    } else {
      /**
       * In case if component is used without utilizing ControlValueAccessor, internal control
       * instance is created, so it can still benefit from built in validators.
       * (dropped) @Output event can be used to get list of files.
       */
      this.controlInstance = new FormControl([], this.validate.bind(this));
      this.onChange = () => {
        this.controlInstance.setValue(this.droppedFiles);
      };
      this.onValidatorChange = () => {
        this.controlInstance.updateValueAndValidity();
      };
      this.onTouched = () => {
        this.controlInstance.markAsTouched();
      };
    }

    this.acceptedExts = this.filesService.extractFileExtensions(this.accept).map(t => `.${t}`);

    this.disabledValidators = { ...this.disabledValidators, ...this.disableValidators };

    this.filesService.loadBytesSizeLimit().then(sizeLimit => {
      this.fileSizeLimit = sizeLimit;
      this.onValidatorChange();
    });
  }

  ngAfterViewInit() {
    /**
     * Emits DragEvent when user drag file inside or outside viewport. Only works when type of dragged elements is 'Files'.
     */
    const draggingFilesGlobally$ = merge(
      fromEvent(document, 'dragenter'),
      fromEvent(document, 'dragleave').pipe(
        // TODO review why this specific filter is needed
        filter((e: DragEvent) => e.pageX === 0 && e.pageY === 0)
      )
    ).pipe(
      filter((ev: DragEvent) => ev.dataTransfer?.types.toString() === 'Files'),
      filter(() => !this.uploadInProgress)
    );

    /**
     * Emits boolean value when file is dragged inside or outside of viewport, or file is not dragged anymore.
     */
    this.isDraggingFiles$ = merge(
      draggingFilesGlobally$.pipe(map(ev => ev.type === 'dragenter')),
      fromEvent(document, 'mouseenter').pipe(map(() => false)),
      fromEvent(document, 'drop').pipe(map(() => false))
    ).pipe(distinctUntilChanged());

    /**
     * Emits boolean value after checking if dragged files match configured extensions list,
     * When dragging of file starts, get mimetypes of each file, convert them to possible extensions list
     * that may be associated with given mimetype, and then check if any of these extensions match configured
     * extensions accept list.
     */
    this.isExtensionAllowed$ = draggingFilesGlobally$.pipe(
      sample(this.isDraggingFiles$.pipe(filter(enlarged => enlarged))),
      switchMap(ev => {
        if (!this.accept) {
          return of(true);
        }

        return of(ev).pipe(
          map(ev => [...(ev.dataTransfer.items as unknown as DataTransferItem[])]),
          map(files =>
            files.map(file => (mimeDB[file.type].extensions as string[] | undefined) || ['unknown'])
          ),
          map(extensionsForFiles =>
            extensionsForFiles.every(extensionsForFile =>
              extensionsForFile.some(ext =>
                this.filesService.extractFileExtensions(this.accept).includes(ext)
              )
            )
          )
        );
      }),
      tap(allowed => {
        this.isExtensionAllowed = allowed;
      })
    );

    /**
     * If File is being dragged and extensions meets requirements, then drop-area can be enlarged.
     */
    this.isDropAreaEnlarged$ = this.isDraggingFiles$;
    this.isDraggedFileUnsupported$ = this.isDraggingFiles$.pipe(
      withLatestFrom(this.isExtensionAllowed$),
      map(([fileDragged, extAllowed]) => fileDragged && !extAllowed)
    );

    /**
     * Emits true when file is hovering above drop-area. Only works when isDropAreaEnlarged$ also emits true.
     * This is workaround for CSS :hover pseudo-class not triggering when dragging file.
     */
    this.isDraggingOverFilePlaceholder$ = merge(
      fromEvent(this.filePlaceholder.nativeElement, 'dragenter'),
      fromEvent(this.filePlaceholder.nativeElement, 'dragleave'),
      fromEvent(this.filePlaceholder.nativeElement, 'drop')
    ).pipe(
      filter((ev: DragEvent) => ev.dataTransfer?.types.toString() === 'Files'),
      map(ev => ev.type === 'dragenter'),
      withLatestFrom(this.isDropAreaEnlarged$),
      map(([draggingOver, dropAreaEnlarged]) => draggingOver && dropAreaEnlarged)
    );
  }

  afterValueChanged() {
    this.onChange([...this.droppedFiles]);
    this.onTouched();
    this.dropped.emit([...this.droppedFiles]);
    this.valid = this.controlInstance.valid;
    this.errors = this.controlInstance.errors;
  }

  onDrop(e: DragEvent) {
    if (this.uploadInProgress) {
      return;
    }

    if (e.dataTransfer.types.toString() !== 'Files') {
      return;
    }
    const files = e.dataTransfer.files;
    if (!files?.length) {
      return;
    }

    for (let i = 0; i < files.length; i++) {
      this.droppedFiles.push(files.item(i));
    }
    this.afterValueChanged();
  }

  filesSelected(ev: Event): void {
    const inputElement = ev.target as HTMLInputElement;
    const files: File[] = Array.from(inputElement.files);
    this.droppedFiles.push(...files);
    inputElement.value = null;
    this.afterValueChanged();
  }

  deleteAt(index: number) {
    this.droppedFiles.splice(index, 1);
    this.afterValueChanged();
  }

  registerOnChange(fn: never): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: never): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  registerOnValidatorChange(fn: () => void): void {
    this.onValidatorChange = fn;
  }

  writeValue(files: File[] | null): void {
    if (!files) {
      files = [];
    }
    this.droppedFiles = [...files];
    this.onValidatorChange();
  }

  @HostListener('window:dragover', ['$event'])
  onDragOver(e) {
    e.preventDefault();
  }

  @HostListener('window:drop', ['$event'])
  onWindowDrop(e) {
    e.preventDefault();
  }

  validate(control: AbstractControl): ValidationErrors | null {
    let errors = {};

    if (!this.disableValidators.fileMaxSize) {
      errors = appendErrors(
        errors,
        validateArrayElements(
          FilePickerValidators.fileMaxSize(this.fileSizeLimit, this.filesService)
        )(control)
      );
    }
    if (!this.disableValidators.fileNonEmpty) {
      errors = appendErrors(
        errors,
        validateArrayElements(FilePickerValidators.fileNonEmpty(this.filesService))(control)
      );
    }
    if (!this.disableValidators.fileNameMaxLength) {
      errors = appendErrors(
        errors,
        validateArrayElements(FilePickerValidators.fileNameMaxLength(this.filesService))(control)
      );
    }
    if (this.accept) {
      errors = appendErrors(
        errors,
        validateArrayElements(
          FilePickerValidators.haveValidExtensions(this.filesService, this.accept)
        )(control)
      );
    }
    if (this.maxAllowedFiles) {
      errors = appendErrors(
        errors,
        FilePickerValidators.fileCountMax(this.maxAllowedFiles)(control)
      );
    }
    if (this.minRequiredFiles) {
      errors = appendErrors(
        errors,
        FilePickerValidators.fileCountMin(this.minRequiredFiles)(control)
      );
    }

    return Object.keys(errors).length ? errors : null;
  }

  private onChange: (files: File[]) => void = () => undefined;
  private onTouched: () => void = () => undefined;
  private onValidatorChange: () => void = () => undefined;
}

function appendErrors(
  errors: ValidationErrors,
  newErrors: ValidationErrors | null
): ValidationErrors {
  return newErrors ? { ...errors, ...newErrors } : errors;
}
