import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { distinctUntilChanged, map, shareReplay } from 'rxjs/operators';
import {
  ExtensionFactory,
  fromTrigger,
  GenericHookType,
  hookGeneric,
  getInjectedHooks,
  stateToFactory,
  ExtensionPointForPlugins,
  HookOptions
} from '../common/extension-hooks';
import { PluginsResolveService } from '../plugins/plugins-resolve.service';
import { Breadcrumb, BreadcrumbItem } from './breadcrumb.model';

/**
 * An extension HOOK can use either a pure value:
 * ```typescript
 *  { provide: HOOK_X, useValue: { ...hookValue }, multi: true }
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  { provide: HOOK_X, useValue: [{ ...hookValues }], multi: true }
 * ```
 *
 * Or an ExtensionFactory which allows to define a get() function. This function
 * gets called on each navigation with the current route and can return values
 * async (observable or promise).
 * ```typescript
 *  { provide: HOOK_X, useFactory: { get: (route) => doSomethingAsync(route) }, multi: true }
 * ```
 */
export type BreadcrumbExtension = Breadcrumb | Breadcrumb[] | ExtensionFactory<Breadcrumb>;

/**
 * @deprecated Consider using the `hookBreadcrumb` function instead.
 */
export const HOOK_BREADCRUMB = new InjectionToken('HOOK_BREADCRUMB');

/**
 * You can either provide a single `Breadcrumb` as parameter:
 * ```typescript
 *  hookBreadcrumb(...)
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  hookBreadcrumb([...])
 * ```
 *
 * Or you provide an Service that implements `ExtensionFactory<Breadcrumb>`
 * ```typescript
 *  export class MyBreadcrumbFactory implements ExtensionFactory<Breadcrumb> {...}
 *  ...
 *  hookBreadcrumb(MyBreadcrumbFactory)
 * ```
 * A typed alternative to `HOOK_BREADCRUMB`.
 * @param breadcrumb The `Breadcrumb`'s or `ExtensionFactory` to be provided.
 * @returns An `Provider` to be provided in your module.
 */
export function hookBreadcrumb(
  breadcrumb: GenericHookType<Breadcrumb>,
  options?: Partial<HookOptions>
) {
  return hookGeneric<Breadcrumb>(breadcrumb, HOOK_BREADCRUMB, options);
}

@Injectable({
  providedIn: 'root'
})
export class BreadcrumbService extends ExtensionPointForPlugins<Breadcrumb> {
  private preferredPath: string;

  constructor(rootInjector: Injector, private router: Router, plugins: PluginsResolveService) {
    super(rootInjector, plugins);
    this.items$ = this.setupItemsObservable();
  }

  get state() {
    return this.state$.value;
  }

  /**
   * Adds a new item to the action bar in the header.
   * @param item The item to add.
   */
  add(item: Breadcrumb) {
    this.state.add(item);
    this.emitNewState();
  }

  /**
   * Removes an action bar item from the header.
   * @param item The item to remove.
   */
  remove(item: Breadcrumb) {
    this.state.delete(item);
    this.emitNewState();
  }

  /**
   * Sets the crumb that contains that path to the top.
   * @param path The path to prefer.
   */
  selectPreferredByPath(path: string) {
    this.preferredPath = path;
    this.refresh();
  }

  sortByPreferredPath(breadcrumbs) {
    if (this.preferredPath) {
      return breadcrumbs.sort(bc =>
        bc.items.find((item: BreadcrumbItem) => !!item.path.match(this.preferredPath)) ? -1 : 1
      );
    }
    return breadcrumbs;
  }

  protected setupItemsObservable(): Observable<Breadcrumb[]> {
    return fromTrigger(this.router, this.refresh$, [
      getInjectedHooks<Breadcrumb>(HOOK_BREADCRUMB, this.injectors),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      distinctUntilChanged(),
      map(breadcrumbs => this.sortByPreferredPath(breadcrumbs)),
      shareReplay(1)
    );
  }
}
