import { Injectable, InjectionToken, Injector, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Observable } from 'rxjs';
import { distinctUntilChanged, filter, first, map, shareReplay } from 'rxjs/operators';
import {
  ExtensionFactory,
  fromTrigger,
  GenericHookType,
  hookGeneric,
  getInjectedHooks,
  stateToFactory,
  ExtensionPointForPlugins,
  HookOptions
} from '../common/extension-hooks';
import { OptionsService } from '../common/options.service';
import { PluginsResolveService } from '../plugins/plugins-resolve.service';
import { Tab } from './tab.model';

/**
 * An extension HOOK can use either a pure value:
 * ```typescript
 *  { provide: HOOK_X, useValue: { ...hookValue }, multi: true }
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  { provide: HOOK_X, useValue: [{ ...hookValues }], multi: true }
 * ```
 *
 * Or an ExtensionFactory which allows to define a get() function. This function
 * gets called on each navigation with the current route and can return values
 * async (observable or promise).
 * ```typescript
 *  { provide: HOOK_X, useFactory: { get: (route) => doSomethingAsync(route) }, multi: true }
 * ```
 */
export type TabExtension = Tab | Tab[] | ExtensionFactory<Tab>;

/**
 * A hook to use for Multi Provider extension.
 * @deprecated Consider using the `hookTab` function instead.
 */
export const HOOK_TABS = new InjectionToken<TabExtension[]>('HOOK_TABS');

/**
 * You can either provide a single `Tab` as parameter:
 * ```typescript
 *  hookTab(...)
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  hookTab([...])
 * ```
 *
 * Or you provide an Service that implements `ExtensionFactory<Tab>`
 * ```typescript
 *  export class MyTabFactory implements ExtensionFactory<Tab> {...}
 *  ...
 *  hookTab(MyTabFactory)
 * ```
 * A typed alternative to `HOOK_TABS`.
 * @param tabs The `Tab`'s or `ExtensionFactory` to be provided.
 * @returns An `Provider` to be provided in your module.
 */
export function hookTab(tabs: GenericHookType<Tab>, options?: Partial<HookOptions>) {
  return hookGeneric<Tab>(tabs, HOOK_TABS, options);
}

/**
 * A service which defines the used tabs.
 */
@Injectable({
  providedIn: 'root'
})
export class TabsService extends ExtensionPointForPlugins<Tab> {
  items$: Observable<Tab[]>;
  orientation$: Observable<'vertical' | 'horizontal'>;

  constructor(
    rootInjector: Injector,
    private router: Router,
    private translateService: TranslateService,
    private options: OptionsService,
    plugins: PluginsResolveService
  ) {
    super(rootInjector, plugins);
    this.items$ = this.setupItemsObservable();

    this.orientation$ = this.items$.pipe(
      map((tabs: Tab[]) => {
        const firstTabWithDefinedOrientation = tabs.find(tab => tab.orientation);
        if (firstTabWithDefinedOrientation) {
          return firstTabWithDefinedOrientation.orientation;
        }
        return this.options.tabsHorizontal ? 'horizontal' : 'vertical';
      }),
      distinctUntilChanged()
    );
  }

  prepareTabs(tabs) {
    return this.sortTabs(
      tabs
        .map(tab => ({
          ...tab,
          template: tab.template instanceof ViewContainerRef ? tab.template : undefined
        }))
        .filter(
          (tab, index, self) => tab.component || index === self.findIndex(t => t.path === tab.path)
        )
    );
  }

  sortTabs(tabs) {
    return tabs.sort((a, b) => {
      const priorityOrder = (b.priority || 0) - (a.priority || 0);

      if (!!priorityOrder) {
        return priorityOrder;
      }

      if (!a.label) {
        return 1;
      }

      if (!b.label) {
        return -1;
      }

      const aTranslatedLabel = this.translateService.instant(a.label);
      const bTranslatedLabel = this.translateService.instant(b.label);
      const translatedLabelOrder = aTranslatedLabel.localeCompare(bTranslatedLabel);

      return translatedLabelOrder;
    });
  }

  /**
   * Returns the current state.
   * @readonly
   * @returns The current set of actions.
   */
  get state(): Set<Tab> {
    return this.state$.value;
  }

  /**
   * Adds a new item to tabs and emits state change
   * @param item The item to add.
   */
  add(item: Tab) {
    this.state.add(item);
    this.emitNewState();
  }

  /**
   * Removes a tab and emits a state change.
   * @param item The item to remove.
   */
  remove(item: Tab) {
    this.state.delete(item);
    this.emitNewState();
  }

  get firstTab$() {
    return this.items$.pipe(
      map(tabs => tabs[0]),
      filter(Boolean),
      first()
    );
  }

  /**
   * Checks if at least one of the tabs is available.
   * @param tabLabels The array of tab labels to check.
   * @returns Returns true if at least one of the tabs is available.
   */
  areAvailable(tabLabels: string[]): boolean {
    const currentTabsSetArray = Array.from(this.state);
    return tabLabels.some(tabLabel => currentTabsSetArray.some(tab => tab.label === tabLabel));
  }

  protected setupItemsObservable(): Observable<Tab[]> {
    return fromTrigger<Tab>(this.router, this.refresh$, [
      getInjectedHooks<Tab>(HOOK_TABS, this.injectors),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      distinctUntilChanged(),
      map(tabs => this.prepareTabs(tabs)),
      shareReplay(1)
    );
  }
}
