import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from './date.pipe';

@Pipe({ name: 'stringifyObject' })
export class StringifyObjectPipe implements PipeTransform {
  constructor(private c8yDatePipe: DatePipe) {}
  transform(value: any): string {
    if (!value) {
      return '';
    }
    if (typeof value === 'object') {
      return JSON.stringify(value);
    }
    if (this.checkDateValidity(value)) {
      return this.c8yDatePipe.transform(value);
    }
    return value;
  }

  checkDateValidity(value): boolean {
    if (!value) return false;
    const isNumericOrFloating = /^-?\d+(\.\d+)?$/.test(value);
    const parsedDate = new Date(value);
    const isValidDate =
      !isNaN(Number(parsedDate)) && Object.prototype.toString.call(parsedDate) === '[object Date]';

    return isValidDate && !isNumericOrFloating;
  }
}
