import { Component, ContentChild, forwardRef, OnDestroy, TemplateRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { toNumber } from 'lodash-es';
import { RangeDirective } from './range.directive';

/**
 * An component which renders a range slider in Cumulocity layout. You need
 * to wrap the default range input in this component.
 *
 * Example:
 * ```
 * <c8y-range>
 *   <input id="range" type="range" min="0" max="100000" value="0" step="1">
 * </c8y-range>
 * ```
 */
@Component({
  selector: 'c8y-range',
  templateUrl: './range.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RangeComponent),
      multi: true
    }
  ]
})
export class RangeComponent implements OnDestroy, ControlValueAccessor {
  /**
   * The range input
   */
  @ContentChild(RangeDirective, { static: true })
  range: RangeDirective;
  /**
   * The range value slider
   */
  @ContentChild('c8yRangeValue', { static: true })
  rangeValue: TemplateRef<unknown>;
  /**
   * The current value.
   */
  value: number;
  /**
   * The current position of the tooltip from left
   */
  left: string;
  /**
   * @ignore
   */
  onChange: (value: number) => void;
  /**
   * @ignore
   */
  onTouched: () => void;

  /**
   * @ignore
   * @param obj The value to update
   */
  writeValue(obj: () => void): void {
    this.range.elementRef.nativeElement.value = obj;
    this.updateValue();
  }

  /**
   * @ignore
   * @param fn The function to register for changes
   */
  registerOnChange(fn: () => void): void {
    this.onChange = fn;
  }

  /**
   * @ignore
   * @param fn The function to register for changes
   */
  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  /**
   * @ignore
   */
  ngAfterContentInit(): void {
    this.updateValue();
    this.range.elementRef.nativeElement.addEventListener('input', () => this.updateValue());
  }

  /**
   * @ignore
   */
  ngOnDestroy(): void {
    this.range.elementRef.nativeElement.removeEventListener('input', () => this.updateValue());
  }

  /**
   * @ignore
   */
  updateValue() {
    const rangeValue = this.range.elementRef.nativeElement.value;
    this.value = toNumber(rangeValue);
    if (this.onChange) {
      this.onChange(this.value);
    }
    this.updatePosition();
  }

  /**
   * Updates the position of the slider
   */
  updatePosition() {
    const range = this.range.elementRef.nativeElement;
    const newValue = Number(((range.value - range.min) * 100) / (range.max - range.min));
    const newPosition = 12 - newValue * 0.24;
    this.left = `calc(${newValue}% + (${newPosition}px))`;
  }
}
