import { Component, ContentChildren, Input, QueryList } from '@angular/core';
import { MessageDirective } from './message.directive';
import { ValidationPattern } from './validation-pattern';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'c8y-messages',
  templateUrl: './messages.component.html'
})
export class MessagesComponent {
  @Input() show: object = {};
  @Input() defaults: object = ValidationPattern.messages;
  @ContentChildren(MessageDirective) customMessages: QueryList<MessageDirective>;
  messages = [];
  messageMap: any = {};

  constructor(private translateService: TranslateService) {}

  ngAfterContentInit(): void {
    this.messageMap = Object.assign(
      {},
      this.defaults,
      this.customMessages.reduce((messages, directive) => {
        messages[directive.name] = directive.text;
        return messages;
      }, {})
    );
  }

  changeVisibility(show: object) {
    this.show = show;
    this.ngOnChanges();
  }

  ngOnChanges() {
    if (this.show) {
      this.messages = Object.keys(this.show)
        .map(key => this.toMessage(key, this.show[key]))
        // if an null message is returned, it will not be displayed
        .filter(message => message !== null);
    } else {
      this.messages = [];
    }
  }

  private toMessage(key, value) {
    let message = this.messageMap[key];
    if (
      (!message || message === ValidationPattern.messages.pattern) &&
      key === 'pattern' &&
      typeof value?.requiredPattern === 'string'
    ) {
      const pattern = value.requiredPattern.replace(/^\//, '').replace(/\/$/, '');
      const foundPattern = Object.keys(ValidationPattern.rules).find(
        tmp => ValidationPattern.rules[tmp].pattern?.source === pattern
      );
      if (foundPattern) {
        key = foundPattern;
        message = this.messageMap[key];
      }
    }
    if (!message) {
      return null;
    }
    return this.translateService.instant(message, value);
  }
}
