import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { HeaderService } from '../../header/header.service';
import { PRODUCT_EXPERIENCE } from '../../shared/core.model';
import { legalNoticesItem } from '../defaults.items';
import { DocLink } from '../docs.models';
import { DocsService } from '../docs.service';

@Component({
  selector: 'c8y-legal-notices-outlet',
  templateUrl: './legal-notices-outlet.component.html'
})
export class LegalNoticesOutletComponent {
  PRODUCT_EXPERIENCE = PRODUCT_EXPERIENCE;
  legalNotices$: Observable<DocLink>;
  drawerOpen$: Observable<boolean>;

  constructor(private docsService: DocsService, private headerService: HeaderService) {
    this.legalNotices$ = this.docsService.items$.pipe(
      map(items => items.find(item => item.type === 'doc' && item.label === legalNoticesItem.label))
    );
    this.drawerOpen$ = this.headerService.rightDrawerOpen$;
  }
}
