import { AfterViewInit, Component, OnDestroy, ViewChild, Input } from '@angular/core';
import { HeaderService } from '../header.service';

@Component({
  selector: 'c8y-title',
  templateUrl: './title.component.html'
})
export class TitleComponent implements AfterViewInit, OnDestroy {
  @Input() pageTitleUpdate = true;
  @ViewChild('template', { static: true }) template;
  private viewInitTimeout;

  constructor(private headerService: HeaderService) {}

  ngAfterViewInit() {
    this.viewInitTimeout = setTimeout(() =>
      this.headerService.changeTitle(this.template, this.pageTitleUpdate)
    );
  }

  ngOnDestroy() {
    if (this.headerService.state.title === this.template) {
      clearTimeout(this.viewInitTimeout);
      this.headerService.changeTitle();
    }
  }
}
