import { Component } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { distinctUntilChanged, filter, map } from 'rxjs/operators';
import { ClipboardService } from '../../clipboard/clipboard.service';
import { AppStateService } from '../../common/ui-state.service';
import { HeaderService } from '../../header/header.service';
import { VersionDetailsModalComponent } from '../version-details-modal/version-details-modal.component';
import { Version } from '../version.model';
import { VersionService } from '../version.service';

@Component({
  selector: 'c8y-version-list',
  templateUrl: './version-list.component.html'
})
export class VersionListComponent {
  open$: Observable<boolean>;
  versionsToDirectlyDisplay$: Observable<Version[]>;
  currentTenantId$: Observable<string>;
  constructor(
    private version: VersionService,
    private appState: AppStateService,
    private clipboardService: ClipboardService,
    private modalService: BsModalService,
    private headerService: HeaderService
  ) {
    this.versionsToDirectlyDisplay$ = this.version.nonHiddenItems$.pipe(
      map(versions => versions.filter(version => !version.hideInRightDrawer).slice(0, 2))
    );
    this.currentTenantId$ = this.appState.currentTenant.pipe(
      map(tenant => tenant?.name),
      filter(tenantId => !!tenantId),
      distinctUntilChanged()
    );
    this.open$ = this.headerService.rightDrawerOpen$;
  }

  openDetailedInfo() {
    this.modalService.show(VersionDetailsModalComponent);
  }

  copyIt(text: string) {
    this.clipboardService.writeText(text);
  }
}
