import { Pipe, PipeTransform } from '@angular/core';
import { groupBy } from 'lodash-es';
import { FilterChip } from '../data-grid.model';

@Pipe({
  name: 'groupedFilterChips'
})
export class GroupedFilterChips implements PipeTransform {
  transform(chips: FilterChip[]): { label: string | undefined; chips: FilterChip[] }[] {
    const groupedChips = groupBy(chips, 'label');

    const result = Object.entries(groupedChips).map(([label, chips]) => ({
      label: label === 'undefined' ? undefined : label,
      chips: chips as FilterChip[]
    }));

    return result.reverse();
  }
}
