import { Inject, Injectable, Injector, Optional } from '@angular/core';
import { Router } from '@angular/router';
import { combineLatest, Observable, timer } from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  mergeMap,
  shareReplay,
  take,
  takeUntil
} from 'rxjs/operators';
import {
  ExtensionPointForPlugins,
  fromTriggerOnce,
  getInjectedHooks,
  stateToFactory
} from '../common/extension-hooks';
import { PluginsResolveService } from '../plugins';
import {
  DynamicComponentDefinition,
  HOOK_COMPONENTS,
  RESOLVING_COMPONENT_WAIT_TIME
} from './dynamic-component.model';
import { DynamicResolverService } from './dynamic-resolver.service';

@Injectable({
  providedIn: 'root'
})
export class DynamicComponentService extends ExtensionPointForPlugins<DynamicComponentDefinition> {
  private DEFAULT_WAIT_TIMEOUT = 5000;

  constructor(
    rootInjector: Injector,
    private router: Router,
    @Optional() @Inject(RESOLVING_COMPONENT_WAIT_TIME) private waitTimeout: number,
    private dynamicResolver: DynamicResolverService,
    private plugins: PluginsResolveService
  ) {
    super(rootInjector, plugins);
    this.items$ = this.setupItemsObservable();
  }

  get state() {
    return this.state$.value;
  }

  /**
   * Returns a component by it's id as an observable.
   * @param id The id of the component.
   */
  getById(id): Promise<DynamicComponentDefinition> {
    if (!id) {
      return Promise.resolve(undefined);
    }
    return this.items$
      .pipe(
        mergeMap((cmps: DynamicComponentDefinition[]) => cmps),
        filter((cmp: DynamicComponentDefinition) => cmp.id === id),
        take(1)
      )
      .toPromise();
  }

  add(component: DynamicComponentDefinition) {
    this.state.add(component);
    this.emitNewState();
  }

  remove(component: DynamicComponentDefinition) {
    this.state.delete(component);
    this.emitNewState();
  }

  async executeResolvers(componentIdAndConfig: Array<{ componentId: string; config: any }>) {
    const dynamicDef = await Promise.all(
      componentIdAndConfig.map(({ componentId }) => this.getById(componentId))
    );
    const result = await this.dynamicResolver.executeResolvers(componentIdAndConfig, dynamicDef);
    return result;
  }

  async serializeConfigs(componentIdAndConfig: Array<{ componentId: string; config: any }>) {
    const dynamicDef = await Promise.all(
      componentIdAndConfig.map(({ componentId }) => this.getById(componentId))
    );
    return this.dynamicResolver.serialize(componentIdAndConfig, dynamicDef);
  }

  protected setupItemsObservable(): Observable<DynamicComponentDefinition[]> {
    const remotesLoaded$ = this.plugins.allPluginsLoaded$.pipe(
      filter(loaded => !!loaded),
      take(1)
    );

    return fromTriggerOnce<DynamicComponentDefinition>(this.router, this.refresh$, [
      getInjectedHooks<DynamicComponentDefinition>(HOOK_COMPONENTS, this.injectors),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      distinctUntilChanged(),
      takeUntil(
        combineLatest([timer(this.waitTimeout || this.DEFAULT_WAIT_TIMEOUT), remotesLoaded$])
      ),
      shareReplay(1)
    );
  }
}
