import { Component, EnvironmentInjector, Injector, Input, ViewChild } from '@angular/core';

@Component({
  selector: 'c8y-breadcrumb-item',
  templateUrl: './breadcrumb-item.component.html'
})
export class BreadcrumbItemComponent {
  @Input() icon: string;
  @Input() translate: boolean;
  @Input() label: string;
  @Input() path: string;
  @Input() injector: Injector;
  @ViewChild('template', { static: false }) template: any;

  constructor(_injector: EnvironmentInjector) {
    this.injector = _injector;
  }
}
