import { Component, EventEmitter, Input, Output } from '@angular/core';
import { BreadcrumbItem } from '../breadcrumb/breadcrumb.model';
import { DashboardComponent } from './dashboard.component';
import { DashboardChange, DashboardSettings, Widget, WidgetChange } from './dashboard.model';
import { DynamicComponentService } from '../dynamic-component/dynamic-component.service';
import { BehaviorSubject, combineLatest, merge, Observable } from 'rxjs';
import { map, shareReplay, switchMap } from 'rxjs/operators';
import { set } from 'lodash-es';
import { WidgetsDashboardEventService } from './widgets-dashboard-event.service';

@Component({
  selector: 'c8y-widgets-dashboard',
  templateUrl: './widgets-dashboard.component.html',
  host: {
    style: `
      display: block;
    `,
    class: 'dashboard c8y-grid-dashboard'
  },
  providers: [WidgetsDashboardEventService]
})
export class WidgetsDashboardComponent {
  @Input()
  set widgets(value: Widget[]) {
    this._widgets.next(value);
  }

  get widgets(): Widget[] {
    return this._widgets.value;
  }

  @Input()
  context: any;

  @Input()
  contextDashboard: any = { updateTarget: true };

  @Input('settings')
  set _settings(settings: Partial<DashboardSettings>) {
    this.settings = { ...this.settings, ...settings };
    this.isLoading$.next(!!this.settings.isLoading);
  }

  @Input()
  breadcrumb: BreadcrumbItem;

  settings: DashboardSettings = {
    isFrozen: false,
    isDisabled: false,
    widgetMargin: 12,
    translateWidgetTitle: false,
    defaultHeight: 4,
    defaultWidth: 4,
    allowFullscreen: false,
    canCopy: true,
    canDelete: true,
    isLoading: false
  };

  @Output()
  onAddWidget: EventEmitter<DashboardComponent> = new EventEmitter();

  @Output()
  onEditWidget: EventEmitter<WidgetChange> = new EventEmitter();

  @Output()
  onDeleteWidget: EventEmitter<WidgetChange> = new EventEmitter();

  @Output()
  onChangeDashboard: EventEmitter<DashboardChange> = new EventEmitter();

  @Output()
  onEditDashboard: EventEmitter<DashboardComponent> = new EventEmitter();

  @Output()
  onCopyDashboard: EventEmitter<DashboardComponent> = new EventEmitter();

  @Output()
  onDeleteDashboard: EventEmitter<DashboardComponent> = new EventEmitter();

  @Output()
  onFreeze: EventEmitter<DashboardSettings> = new EventEmitter();

  @Output()
  onChangeStart: EventEmitter<WidgetChange> = new EventEmitter();

  @Output()
  onChangeEnd: EventEmitter<WidgetChange> = new EventEmitter();

  resolvedWidgets$: Observable<Widget[]>;

  isLoadingWidgets$: Observable<boolean>;

  private _widgets = new BehaviorSubject<Widget[]>([]);
  private isLoading$ = new BehaviorSubject(!!this._settings?.isLoading);
  private isExecutingResolvers$: Observable<boolean>;

  constructor(private dynamic: DynamicComponentService) {
    this.resolvedWidgets$ = this._widgets.pipe(
      switchMap(widgets => this.executeResolversOfWidgets(widgets)),
      shareReplay(1)
    );
    this.isExecutingResolvers$ = merge(
      this._widgets.pipe(map(() => true)),
      this.resolvedWidgets$.pipe(map(() => false))
    );
    this.isLoadingWidgets$ = combineLatest([this.isLoading$, this.isExecutingResolvers$]).pipe(
      map(loadings => loadings.some(loading => loading)),
      shareReplay(1)
    );
  }

  toggleFullscreen() {
    const elem = document.body as any;
    const doc = document as any;
    if (!this.fullScreen()) {
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.msRequestFullscreen) {
        elem.msRequestFullscreen();
      } else if (elem.mozRequestFullScreen) {
        elem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullscreen) {
        elem.webkitRequestFullscreen((Element as any).ALLOW_KEYBOARD_INPUT);
      }
    } else if (doc.exitFullscreen) {
      doc.exitFullscreen();
    } else if (doc.msExitFullscreen) {
      doc.msExitFullscreen();
    } else if (doc.mozCancelFullScreen) {
      doc.mozCancelFullScreen();
    } else if (doc.webkitExitFullscreen) {
      doc.webkitExitFullscreen();
    }
  }

  fullScreen() {
    const doc: any = document;
    return !!(
      doc.fullscreenElement ||
      doc.mozFullScreenElement ||
      doc.webkitFullscreenElement ||
      doc.msFullscreenElement
    );
  }

  updateWidgetClasses(widget: Widget, classes: { [key: string]: boolean }) {
    widget.classes = { ...widget.classes, ...classes };
  }

  updateWidgetConfig(data: any, widget: Widget) {
    widget.config = { ...widget.config, ...data };
  }

  private async executeResolversOfWidgets(widgets: Widget[]): Promise<Widget[]> {
    if (!widgets) {
      return widgets;
    }

    const tuple = widgets.map(widget => ({
      componentId: widget.componentId || widget.name,
      config: widget.config
    }));

    const resolvedResults = await this.dynamic.executeResolvers(tuple);
    widgets.forEach((widget, index) => {
      if (resolvedResults[index]) {
        if (!widget.config) {
          widget.config = {};
        }
        Object.entries(resolvedResults[index]).forEach(([key, value]) =>
          set(widget.config, key, value)
        );
      }
    });
    return widgets;
  }
}
