import { Component, Attribute, HostBinding } from '@angular/core';
import { Alert } from './alert.model';
import { AlertService } from './alert.service';
import { BehaviorSubject } from 'rxjs';
import { AlertOutletBase } from './alert-outlet-base';

/**
 * Alert outlet component shows all added alerts. It is used in the application's `c8y-bootstrap` component.
 *
 * For applications which dont use `c8y-bootstrap` component but want to display alerts via AlertService, a c8y-alert-outlet component needs to be added explicitly in the template.
 *
 * ## Example:
 * ```html
 *  <div class="alerts">
 *    <c8y-alert-outlet></c8y-alert-outlet>
 *  </div>
 * ```
 */
@Component({
  selector: 'c8y-alert-outlet',
  templateUrl: './alert-outlet.component.html'
})
export class AlertOutletComponent extends AlertOutletBase {
  /**
   * @ignore
   */
  @HostBinding('class.static') isStatic = false;
  /**
   * The array of current alerts.
   */
  get alerts(): BehaviorSubject<Alert[]> {
    return this.alertService.state$;
  }
  /**
   * @ignore
   */
  constructor(@Attribute('position') private position: string, public alertService: AlertService) {
    super(alertService);
    this.isStatic = this.position === 'static';
  }
  /**
   * Returns a CSS class for an alert depending on its type to style the corresponding alert component.
   */
  getClass(alert: Alert) {
    const classes = [];
    if (alert.type !== 'danger') {
      classes.push('interact');
    }
    classes.push(`alert-${alert.type}`);
    return classes.join(' ');
  }
  /**
   * Executes alerts onClose method, if provided, and closes the alert.
   * @param alert The alert which will be closed.
   */
  close(alert: Alert) {
    if (alert.onClose) {
      alert.onClose();
    }
    this.alertService.remove(alert);
  }
}
