import { Component, Input, TemplateRef } from '@angular/core';
import { Observable, of, pipe } from 'rxjs';
import { map, tap } from 'rxjs/operators';

@Component({
  selector: 'c8y-virtual-scroller-wrapper',
  templateUrl: './virtual-scroller-wrapper.component.html'
})
export class VirtualScrollerWrapperComponent {
  @Input() items;
  @Input() itemHeight;
  @Input() containerHeight;
  @Input() template: TemplateRef<any>;

  items$: Observable<unknown>;

  @Input() filterPipe = pipe(tap());

  @Input()
  strategy: 'fixed' | 'window';

  @Input() trackByFn = (i, item) => {
    return item.id;
  };

  apply() {
    this.items$ = of(this.items).pipe(
      src => this.filterPipe(src),
      map(item => item)
    );
  }
}
