import { Pipe, PipeTransform } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Column, FilterChip } from '../data-grid.model';
import { FilterMapperService } from './filter-mapper.service';

@Pipe({
  name: 'mapToFilterChips'
})
export class FilterMapperPipe implements PipeTransform {
  constructor(private filterMapperService: FilterMapperService) {}

  transform(column: Column): Observable<FilterChip[]> {
    const filterMapperService = this.filterMapperService;

    const filterObject: FilterChip = {
      value: '',
      displayValue: '',
      path: [],
      externalFilterQuery: column.externalFilterQuery,
      filteringConfig: column.filteringConfig,
      columnName: column.name,
      remove() {
        return filterMapperService.removeChip(this);
      }
    };

    if (column.filterPredicate) {
      const chip: FilterChip = {
        value: column.filterPredicate,
        displayValue: column.filterPredicate as string,
        filterPredicate: column.filterPredicate as string,
        columnName: column.name,
        remove() {
          return {
            columnName: this.columnName,
            filterPredicate: this.filterPredicate
          };
        }
      };
      return of([chip]);
    }

    return filterMapperService.getMappedFilterValues(filterObject);
  }
}
