import {
  Component,
  EventEmitter,
  forwardRef,
  HostListener,
  Input,
  Output,
  ViewChild
} from '@angular/core';
import { assignWith } from 'lodash-es';
import { GainsightService } from '../product-experience/gainsight.service';
import {
  ProductExperienceEvent,
  ProductExperienceEventSource,
  PRODUCT_EXPERIENCE_EVENT_SOURCE
} from '../product-experience/product-experience.model';

/**
 * Actions are functions that can be executed on the
 * current list item. They are displayed next to the
 * item and can be grouped in a dropdown.
 *
 * @example
 * ```html
 * <c8y-li>
 *   <c8y-li-action (click)="deleteDevice(device.id)" icon="times">
 *     Delete
 *   </c8y-li-action>
 *   <c8y-li-action (click)="(false)" icon="rocket">
 *     Launch to space
 *   </c8y-li-action>
 * </c8y-li>
 * ```
 */
@Component({
  selector: 'c8y-list-item-action, c8y-li-action',
  templateUrl: './list-item-action.component.html',
  providers: [
    {
      provide: PRODUCT_EXPERIENCE_EVENT_SOURCE,
      useExisting: forwardRef(() => ListItemActionComponent)
    }
  ]
})
export class ListItemActionComponent implements ProductExperienceEventSource {
  /**
   * @ignore
   */
  @ViewChild('template', { static: false })
  template: any;
  /**
   * The label to display (you can alternatively use
   * content projection to display the label).
   */
  @Input()
  label: string;
  /**
   * The name of the icon to use
   */
  @Input()
  icon: string;
  /**
   * An EventEmitter to determine what to do if the
   * action is triggered.
   */
  @Output()
  click = new EventEmitter<any>();

  productExperienceEvent: ProductExperienceEvent;

  constructor(private gainsightService: GainsightService) {}

  @HostListener('click', ['$event.target'])
  onClick(event: HTMLElement) {
    const element = event.closest('[title]') || event.closest('[uib-tooltip]');
    if (!element) {
      return;
    }
    const eventName = this.productExperienceEvent?.eventName || 'listItemAction';
    const actionName = element.getAttribute('title') || element.getAttribute('uib-tooltip');
    const action = this.gainsightService.translateToEnglish(actionName);
    // Assign url and action to event data only if they are not already defined
    const eventData = assignWith(
      this.productExperienceEvent?.data,
      {
        url: element.baseURI,
        action
      },
      (targetVal, sourceVal) => sourceVal ?? targetVal
    );

    this.gainsightService.triggerEvent(eventName, eventData);
  }
}
