import { Pipe, PipeTransform } from '@angular/core';
import { ApplicationService, IApplication } from '@c8y/client';

@Pipe({
  name: 'appHref'
})
export class AppHrefPipe implements PipeTransform {
  constructor(private applications: ApplicationService) {}

  transform(app: IApplication): string {
    return this.applications.getHref(app);
  }
}
