import { CdkHeaderCell } from '@angular/cdk/table';
import {
  AfterViewInit,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  forwardRef,
  Inject,
  Input,
  OnChanges,
  Optional,
  Output,
  QueryList,
  ViewChild,
  ViewChildren,
  ViewContainerRef
} from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { IResultList, Paging } from '@c8y/client';
import {
  flow,
  get,
  indexOf,
  isEmpty,
  isNil,
  parseInt as _parseInt,
  union,
  uniqBy,
  without
} from 'lodash-es';
import { BsDropdownDirective } from 'ngx-bootstrap/dropdown';
import { BsModalService } from 'ngx-bootstrap/modal';
import {
  BehaviorSubject,
  combineLatest,
  isObservable,
  merge,
  Observable,
  of,
  pipe,
  Subject,
  UnaryFunction
} from 'rxjs';
import {
  concatMap,
  debounceTime,
  delay,
  distinctUntilChanged,
  filter,
  first,
  map,
  mergeMap,
  switchMap,
  take,
  takeUntil,
  tap
} from 'rxjs/operators';
import { AlertService } from '../alert/alert.service';
import { toObservable } from '../common/extension-hooks';
import { LoadMoreComponent } from '../common/load-more.component';
import { LoadMoreMode } from '../common/load-more.model';
import { gettext } from '../i18n/gettext';
import { PopoverConfirmButtons, PopoverConfirmComponent } from '../modal/popover-confirm.component';
import { GainsightService } from '../product-experience/gainsight.service';
import {
  ProductExperienceEvent,
  ProductExperienceEventSource,
  PRODUCT_EXPERIENCE_EVENT_SOURCE
} from '../product-experience/product-experience.model';
import { CellRendererSpec } from './column/cell-renderer';
import { ColumnDirective } from './column/column.directive';
import { CustomColumn } from './column/custom.column';
import { FilteringFormRendererSpec, FilteringModifier } from './column/filtering-form-renderer';
import { ConfigureCustomColumnComponent } from './configure-custom-column';
import {
  DataGridConfigurationStrategy,
  DATA_GRID_CONFIGURATION_STRATEGY,
  GridConfigPart,
  GridEventType
} from './data-grid-configuration.model';
import {
  ActionControl,
  BuiltInActionType,
  BulkActionControl,
  Column,
  ColumnConfig,
  ColumnDataType,
  CustomColumnConfig,
  DataGrid,
  DataSourceStats,
  DisplayOptions,
  FilterChip,
  FilteringActionType,
  GridConfig,
  HeaderActionControl,
  minColumnGridTrackSize,
  Pagination,
  ratiosByColumnTypes,
  Row,
  ServerSideDataCallback,
  SortOrder,
  SpecialColumnName
} from './data-grid.model';
import { DataGridService } from './data-grid.service';
import { GridDataSource } from './grid-data-source';
import { PX_ACTIONS, PX_EVENT_NAME } from './product-experience.constants';

enum SortingOrder {
  ASC = 'asc',
  DESC = 'desc'
}

@Component({
  selector: 'c8y-data-grid',
  templateUrl: './data-grid.component.html',
  host: { class: 'd-contents' },
  providers: [
    {
      provide: PRODUCT_EXPERIENCE_EVENT_SOURCE,
      useExisting: forwardRef(() => DataGridComponent)
    }
  ]
})
export class DataGridComponent
  implements DataGrid, OnChanges, AfterViewInit, ProductExperienceEventSource
{
  /** The title for the data grid, it's displayed in the grid's header. */
  @Input() title: string = gettext('Items');
  /** The label for load more button. */
  @Input() loadMoreItemsLabel: string = gettext('Load more items');
  /** The label for loading indicator. */
  @Input() loadingItemsLabel: string = gettext('Loading items');
  /** Determines whether text search input is shown in the grid's header. */
  @Input() showSearch = false;
  /** Takes an event emitter. When an event is emitted, the grid will be reloaded. */
  @Input() refresh: EventEmitter<any>;

  columns: Column[] = [];
  /** The list of columns to be displayed in the grid. */
  @Input('columns')
  private _columns: Column[];

  dataSource = new GridDataSource();
  rows: Row[];
  /** The list of rows to be displayed in the grid (used for client side data). */
  @Input('rows')
  set _rows(rows: Row[]) {
    this.rows = rows || [];
  }

  filteringLabelsParams = {
    filteredItemsCount: 0,
    allItemsCount: 0
  };

  pagination: Pagination;
  paginationLabelParams = {
    pageFirstItemIdx: 0,
    pageLastItemIdx: 0,
    itemsTotal: 0
  };
  lastClickedRow: Row;
  readonly possiblePageSizes: number[] = [25, 50, 100];
  readonly minPossiblePageSize = Math.min(...this.possiblePageSizes);
  /** Pagination settings, e.g. allows for setting current page or page size. */
  @Input('pagination')
  set _pagination(pagination: Pagination) {
    this.pagination = pagination;
  }

  infiniteScroll: LoadMoreMode;
  /** Sets load more mode. */
  @Input('infiniteScroll')
  set _infiniteScroll(infiniteScroll: LoadMoreMode) {
    this.infiniteScroll = infiniteScroll;
  }

  serverSideDataCallback: ServerSideDataCallback;
  /**
   * Sets a callback function which will be invoked whenever data needs to be loaded from server.
   * The function should take [[DataSourceModifier]] and return [[ServerSideDataResult]].
   */
  @Input('serverSideDataCallback')
  set _serverSideDataCallback(serverSideDataCallback: ServerSideDataCallback) {
    this.serverSideDataCallback = serverSideDataCallback;
  }

  selectable = false;
  /** Determines whether items can be selected by clicking a checkbox in the first column. */
  @Input('selectable')
  set _selectable(selectable: boolean) {
    this.selectable = selectable;
  }

  singleSelection = false;
  /** Restricts selection to a single row only. Selection column displays radio button instead of checkboxes */
  @Input('singleSelection')
  set _singleSelection(singleSelection: boolean) {
    this.singleSelection = singleSelection;
  }

  selectionPrimaryKey = 'id';
  /** Determines which item's property will be used to distinguish selection status. */
  @Input('selectionPrimaryKey')
  set _selectionPrimaryKey(selectionPrimaryKey: string) {
    this.selectionPrimaryKey = selectionPrimaryKey;
  }

  displayOptions: DisplayOptions = {
    striped: true,
    bordered: false,
    gridHeader: true,
    filter: true
  };
  /** Sets display options. */
  @Input('displayOptions')
  set _displayOptions(displayOptions: DisplayOptions) {
    this.displayOptions = { ...this.displayOptions, ...displayOptions };
  }

  actionControls: ActionControl[];
  /** Sets action controls (actions available for individual items). */
  @Input('actionControls')
  set _actionControls(actionControls: ActionControl[]) {
    this.actionControls = actionControls || [];
  }

  bulkActionControls: BulkActionControl[];
  /** Sets bulk action controls (actions available for items selected by user). */
  @Input('bulkActionControls')
  set _bulkActionControls(bulkActionControls: BulkActionControl[]) {
    this.bulkActionControls = bulkActionControls || [];
  }

  headerActionControls: HeaderActionControl[];
  /** Sets header action controls (actions available from data grid header). */
  @Input('headerActionControls')
  set _headerActionControls(headerActionControls: HeaderActionControl[]) {
    this.headerActionControls = headerActionControls || [];
  }

  /** Sets initial search text. */
  @Input()
  searchText = '';

  /** Determines if custom columns button will be enabled. */
  @Input()
  configureColumnsEnabled = true;

  /** Shows the warning for the sub-assets counter */
  @Input()
  showCounterWarning = false;

  /**
   * Sets the class name used for active rows (last clicked).
   * Set empty string to disable appending active class to grid rows.
   */
  @Input()
  activeClassName = 'active';

  /** Emits an event when mouse is over a row. */
  @Output() rowMouseOver = new EventEmitter<object>();
  /** Emits an event when mouse leaves a row. */
  @Output() rowMouseLeave = new EventEmitter<object>();
  /** Emits an event when a row is clicked. */
  @Output() rowClick = new EventEmitter<object>();
  /** Emits an event when grid's configuration is changed. */
  @Output() onConfigChange = new EventEmitter<GridConfig>();
  /** Emits an event before the filter is applied. */
  @Output() onBeforeFilter = new EventEmitter<object>();
  /** Emits an event before the search is performed. */
  @Output() onBeforeSearch = new EventEmitter<string>();
  /** Emits an event when a filter is applied in a column. */
  @Output() onFilter = new EventEmitter<{
    columnName?: string;
    dropdown?: any;
    filteringModifier?: any;
  }>();
  /** Emits an event when items selection changes. The array contains keys of selected items (key property is defined by `selectionPrimaryKey`). */
  @Output() itemsSelect = new EventEmitter<string[]>();
  /** Emits an event when reload button is clicked. */
  @Output() onReload = new EventEmitter();
  /** Emits an event when a custom column is added */
  @Output() onAddCustomColumn = new EventEmitter<CustomColumnConfig>();
  /** Emits an event when a custom column is removed */
  @Output() onRemoveCustomColumn = new EventEmitter<Column>();
  /** Emits an event after the column filter has been reset */
  @Output() onColumnFilterReset = new EventEmitter<Column>();
  /** Emits an event when column sorting has been changed */
  @Output() onSort = new EventEmitter<GridConfig>();
  /** Emits an event when page size has been changed */
  @Output() onPageSizeChange = new EventEmitter<GridConfig>();
  /** Emits an event when column order has been changed */
  @Output() onColumnReordered = new EventEmitter<GridConfig>();
  /** Emits an event when column order has been changed */
  @Output() onColumnVisibilityChange = new EventEmitter<GridConfig>();

  @ContentChildren(ColumnDirective) columnRenderers: QueryList<ColumnDirective>;

  @ViewChild('scroll', { static: true }) scrollContainer: ElementRef;

  @ViewChild('infiniteScrollContainer', { static: false, read: ViewContainerRef })
  infiniteScrollContainer: ViewContainerRef;

  loadMoreComponent: LoadMoreComponent;

  columnNames = [];

  styles = {
    tableCursor: 'auto',
    gridTemplateColumns: undefined,
    gridInfiniteScrollColumn: undefined
  };

  searchText$ = new EventEmitter<string>();
  filteringApplied = false;
  columnsWithFiltersApplied: Column[] = [];

  totalPagesCount$ = new BehaviorSubject<number>(Infinity);
  hidePagination$ = this.totalPagesCount$.pipe(
    map(totalPagesCount => totalPagesCount <= 1),
    delay(0) // prevents ExpressionChangedAfterItHasBeenCheckedError
  );

  selectedItemIds: string[] = [];
  currentPageSelectionState = {
    allSelected: false,
    allDeselected: true
  };

  builtInActionType = {
    Edit: BuiltInActionType.Edit,
    Delete: BuiltInActionType.Delete,
    Export: BuiltInActionType.Export
  };

  confirmRemoveColumnButtons: PopoverConfirmButtons[] = [
    {
      label: gettext('Cancel'),
      action: () => Promise.resolve(false)
    },
    {
      label: gettext('Remove`column,verb`'),
      status: 'danger',
      action: () => Promise.resolve(true)
    }
  ];

  isConfigContextKnown = false;

  /** Product experience constants declarations */
  productExperienceEvent: ProductExperienceEvent = { eventName: PX_EVENT_NAME };
  PX_ACTIONS = PX_ACTIONS;

  readonly sortColumnTitle = gettext('Sort column "{{ name }}"');

  resizeHandleMouseDown$ = new EventEmitter<any>();
  resizeHandleContainerMouseMove$ = new EventEmitter<any>();
  resizeHandleContainerMouseUp$ = new EventEmitter<any>();

  filtersHelpPopoverHtml: string = gettext('Click the column headers to apply filters.');

  private headerBeingResized: {
    columnName: string;
    el: HTMLElement;
  };
  @ViewChildren(CdkHeaderCell, { read: ElementRef }) private thRefs: QueryList<ElementRef>;
  private thEls: HTMLElement[];

  private recreateLoadMoreComponent: boolean;

  private columnsInitialized = false;

  private defaultColumns: Column[] = [];

  private reloadConfiguration$: Subject<void> = new Subject();

  private unsubscribe$ = new Subject<any>();

  private readonly SEARCH_DEBOUNCE_TIME = 500;

  /**
   * Event emitter, taking boolean values used for loading data grid data with debounce.
   * Default value is set to false. Set to true if data grid is using infinite scroll and page should be reloaded.
   * This is used to avoid having multiple this.loadData() function calls.
   */
  private triggerLoadData = new EventEmitter<boolean>();

  constructor(
    @Optional()
    @Inject(DATA_GRID_CONFIGURATION_STRATEGY)
    public configurationStrategy: DataGridConfigurationStrategy,
    private dataGridService: DataGridService,
    private sanitizer: DomSanitizer,
    private gainsightService: GainsightService,
    private bsModalService: BsModalService,
    private alertService: AlertService
  ) {
    this.triggerLoadData.pipe(debounceTime(1), takeUntil(this.unsubscribe$)).subscribe(reload => {
      this.loadData(reload);
    });

    this.reloadConfiguration$
      .pipe(
        switchMap(() => this.configurationStrategy?.getConfig$() ?? of(null)),
        tap(config => {
          this.setColumns(config);
          this.setPageSize(config);
          this.triggerLoadData.emit(!!this.infiniteScroll);
        }),
        switchMap(() => this.dataSource.stats$),
        tap(stats => {
          this.createLoadMoreComponent(stats);
          this.updateFilteringLabelsParams(stats);
          this.updatePaginationLabelParams(stats);
        }),
        takeUntil(this.unsubscribe$)
      )
      .subscribe();
  }

  ngOnInit() {
    this.isConfigContextKnown = !!this.configurationStrategy?.isContextKnown();

    this.searchText$
      .pipe(
        takeUntil(this.unsubscribe$),
        debounceTime(this.SEARCH_DEBOUNCE_TIME),
        distinctUntilChanged(),
        tap(searchText => {
          this.searchText = searchText;
          this.onBeforeSearch.emit(this.searchText);
          this.triggerEvent({
            action: PX_ACTIONS.SEARCH,
            searchInput: searchText
          });
        })
      )
      .subscribe(() => {
        this.reload();
      });

    if (this.selectable) {
      combineLatest(this.dataSource.data$, this.itemsSelect.asObservable())
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(([data]) => {
          const currentPageEmpty = data.length === 0;

          this.currentPageSelectionState = {
            allSelected: currentPageEmpty ? false : data.every(item => this.isItemSelected(item)),
            allDeselected: currentPageEmpty ? true : data.every(item => !this.isItemSelected(item))
          };
        });
    }

    this.reloadConfiguration$.next();

    if (this.refresh) {
      this.refresh.pipe(takeUntil(this.unsubscribe$)).subscribe(() => {
        this.cancel();
        this.reload();
      });
    }

    this.processAndPersistConfigChange();
  }

  ngOnChanges(event) {
    if (
      ((!event._actionControls && !event.searchText) || event._actionControls?.firstChange) &&
      this.columnsInitialized
    ) {
      const reload = !!event._infiniteScroll?.currentValue && !event._infiniteScroll?.firstChange;
      this.triggerLoadData.emit(reload);
    }
    if (!!event._columns && !event._columns.firstChange) {
      this.reloadConfiguration$.next();
    }
    this.updateColumns();
  }

  ngAfterViewInit() {
    this.updateGridColumnsSize();
    this.updateThEls();
    this.setupResizeHandle();
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  setColumns(config: GridConfig) {
    if (!!this.configurationStrategy && !isEmpty(this._columns)) {
      this.columns = this.dataGridService.applyConfigToColumns(config, this._columns);
      this.columnsInitialized = true;
    } else {
      this.columns = this._columns || [];
      this.columnsInitialized = this.columnsInitialized || !!this._columns;
    }

    this.defaultColumns = this.columns || [];
    this.updateColumns();
  }

  setPageSize(config: GridConfig) {
    if (!!config?.pagination) {
      this.pagination = {
        ...this.pagination,
        pageSize: config.pagination.pageSize
      };
    }

    const pageSize = get(this.pagination, 'pageSize');
    if (
      this.pagination &&
      !this.possiblePageSizes.find(possiblePageSize => possiblePageSize === pageSize)
    ) {
      this.pagination = { ...this.pagination, pageSize: this.minPossiblePageSize };
    }
  }

  openCustomColumnModal(): void {
    const modalRef = this.bsModalService.show(ConfigureCustomColumnComponent, {
      class: 'modal-sm',
      ariaDescribedby: 'modal-body',
      ariaLabelledBy: 'modal-title',
      ignoreBackdropClick: true,
      initialState: {
        columns: this.columns
      }
    });

    modalRef.content.onAddCustomColumn
      .pipe(
        tap((customColumnConfig: CustomColumnConfig) => {
          const firstFixedColumPosition = this.columns.indexOf(
            this.columns.find(column => column.positionFixed)
          );
          this.columns.splice(
            firstFixedColumPosition > -1 ? firstFixedColumPosition : this.columns.length,
            0,
            new CustomColumn(customColumnConfig)
          );
          this.updateColumns();
          this.triggerEvent({
            action: PX_ACTIONS.ADD_CUSTOM_COLUMN,
            column: customColumnConfig.header || customColumnConfig.name
          });
        }),
        takeUntil(modalRef.onHidden)
      )
      .subscribe(event => this.onAddCustomColumn.emit(event));
  }

  async removeCustomColumn(
    poConfirm: PopoverConfirmComponent,
    column: Column,
    ddConfigureColumns: BsDropdownDirective
  ) {
    ddConfigureColumns.autoClose = false;
    poConfirm.message = gettext('Do you want to remove this column?');
    try {
      const remove: boolean = await poConfirm.show(this.confirmRemoveColumnButtons);
      if (remove) {
        this.columns = this.columns.filter(col => col?.name !== column?.name);
        this.updateColumns();
        this.onRemoveCustomColumn.emit(column);
        this.triggerEvent({
          action: PX_ACTIONS.REMOVE_CUSTOM_COLUMN,
          column: column.header || column.name
        });
      }
    } catch (e) {
      this.alertService.addServerFailure(e);
    }

    setTimeout(() => (ddConfigureColumns.autoClose = true), 0);
  }

  async removeFilter(filter: Partial<FilterChip>) {
    const filteringModifier = filter.externalFilterQuery
      ? { externalFilterQuery: filter.externalFilterQuery }
      : { filterPredicate: filter.filterPredicate };
    this.onBeforeFilter.emit({
      columnName: filter.columnName,
      dropdown: undefined,
      filteringModifier
    });
    if (
      (filter.externalFilterQuery && !this.checkIfAnyValuesExist(filter.externalFilterQuery)) ||
      filter.filterPredicate
    ) {
      this.updateFiltering([filter.columnName], {
        type: FilteringActionType.ResetFilter
      });

      this.onFilter.emit({ columnName: filter.columnName });
    } else {
      this.updateFiltering([filter.columnName], {
        type: FilteringActionType.ApplyFilter,
        payload: { filteringModifier }
      });
      this.onFilter.emit({
        columnName: filter.columnName,
        dropdown: undefined,
        filteringModifier
      });
    }

    this.triggerEvent({
      action: PX_ACTIONS.REMOVE_FILTER,
      column: filter.columnName,
      filteringModifier
    });
  }

  trackByName(index, item) {
    return item.name;
  }

  resolveCellValue(row, path) {
    return flow([
      x => this.dataSource.resolveValue(x, path),
      this.dataSource.resolveFunction,
      this.dataSource.normalizeNil
    ])(row);
  }

  changeSortOrder(columnName) {
    const column = this.columns.find(({ name }) => name === columnName);

    if (column) {
      const { sortOrder } = column;

      if (!sortOrder) {
        this.updateSorting([columnName], SortingOrder.ASC);
      } else if (sortOrder === SortingOrder.ASC) {
        this.updateSorting([columnName], SortingOrder.DESC);
      } else {
        this.updateSorting([columnName], '');
      }
    }
  }

  updateSorting(columnNames: string[], sortOrder: SortOrder) {
    this.triggerEvent({
      action: PX_ACTIONS.CHANGE_SORTING_ORDER,
      columns: columnNames,
      sortOrder: sortOrder === '' ? 'none' : sortOrder
    });

    this.columns = this.columns.map((column): Column => {
      if (columnNames.includes(column.name)) {
        return { ...column, sortOrder };
      }

      return column;
    });
    this.emitConfigChange('sort');

    this.reload();
  }

  applyFilter(columnName, dropdown, filteringModifier) {
    this.triggerEvent({
      action: PX_ACTIONS.APPLY_FILTER,
      column: columnName,
      filteringModifier
    });
    this.onBeforeFilter.emit({ columnName, dropdown, filteringModifier });
    this.updateFiltering([columnName], {
      type: FilteringActionType.ApplyFilter,
      payload: { filteringModifier }
    });
    dropdown.hide();
    this.onFilter.emit({ columnName, dropdown, filteringModifier });
  }

  resetFilter(columnName, dropdown) {
    this.triggerEvent({ action: PX_ACTIONS.RESET_FILTER, column: columnName });
    this.updateFiltering([columnName], { type: FilteringActionType.ResetFilter });
    dropdown.hide();
    this.onFilter.emit({ columnName, dropdown });
  }

  clearFilters(reload = true) {
    this.updateFiltering(
      this.columns.map(({ name }) => name),
      {
        type: FilteringActionType.ResetFilter
      },
      reload
    );
    this.onFilter.emit({});
    this.triggerEvent({ action: PX_ACTIONS.CLEAR_FILTER });
  }

  updateFiltering(
    columnNames: string[],
    action: {
      type: FilteringActionType;
      payload?: { filteringModifier: FilteringModifier };
    },
    reload = true
  ) {
    this.columns = this.columns.map(column => {
      if (columnNames.includes(column.name)) {
        return {
          ...column,
          ...(action.type === FilteringActionType.ApplyFilter
            ? action.payload.filteringModifier
            : this.onResetFilterAction(column))
        };
      }
      return column;
    });

    this.updateFilteringApplied();

    if (reload) {
      this.reload();
    }
  }

  updateFilteringApplied() {
    this.columnsWithFiltersApplied = this.columns.filter(this.isColumnFilteringApplied);
    this.filteringApplied = this.columnsWithFiltersApplied.length > 0;

    this.filtersHelpPopoverHtml = this.filteringApplied
      ? gettext(
          'Click the column headers to apply filters. Click <b>Active filters</b> button to manage applied filters.'
        )
      : gettext('Click the column headers to apply filters.');
  }

  isColumnFilteringApplied(column: Column): boolean {
    const { filterable, filterPredicate, externalFilterQuery } = column;

    return !!(filterable && (filterPredicate || externalFilterQuery));
  }

  updatePagination({ itemsPerPage, page }) {
    const configChanged = this.pagination?.pageSize !== itemsPerPage;
    this.pagination = { ...this.pagination, pageSize: itemsPerPage, currentPage: page };
    this.loadData();
    if (configChanged) {
      this.emitConfigChange('pagination');
    }
    this.triggerEvent({ action: PX_ACTIONS.CHANGE_PAGINATION, itemsPerPage, page });
  }

  clickReload() {
    this.searchText = '';
    this.reload();
    this.onReload.next();
    this.triggerEvent({ action: PX_ACTIONS.RELOAD });
  }

  reload() {
    this.pagination = { ...this.pagination, currentPage: 1 };
    this.recreateLoadMoreComponent = true;
    this.loadData(true);
    this.scrollToTop();
  }

  loadNextPage(): Promise<IResultList<object>> {
    this.pagination = { ...this.pagination, currentPage: this.pagination.nextPage };

    this.loadData();

    return this.dataSource.resultList$
      .pipe(take(1)) // in order for `toPromise` to work, the observable needs to complete
      .toPromise()
      .then(result => {
        return {
          ...result,
          paging: {
            ...result.paging,
            next: this.loadNextPage.bind(this)
          }
        } as IResultList<object>;
      });
  }

  getCellRendererSpec({ value, row, columnName }): CellRendererSpec {
    return this._getCellRendererSpec({ type: 'CELL', value, row, columnName });
  }

  getHeaderCellRendererSpec({ value, columnName }): CellRendererSpec {
    return this._getCellRendererSpec({ type: 'HEADER', value, row: undefined, columnName });
  }

  getFilteringFormRendererSpec({ column, dropdown }): FilteringFormRendererSpec {
    return {
      renderer:
        get(this.getColumnRenderer(column), 'filteringFormRendererDef.template') ||
        column.filteringFormRendererComponent,
      context: {
        property: column,
        applyFilter: this.applyFilter.bind(this, column.name, dropdown),
        resetFilter: this.resetFilter.bind(this, column.name, dropdown)
      }
    };
  }

  setAllItemsSelected(selected) {
    this.dataSource.selection$
      .pipe(first())
      .subscribe(({ filteredDataIds }) => this.setItemsSelected(filteredDataIds, selected));
  }

  setAllItemsInCurrentPageSelected(selected) {
    this.dataSource.data$.pipe(first()).subscribe(data => this.setItemsSelected(data, selected));
  }

  setItemsSelected(items: any[], selected) {
    const itemIds = items.map((item: object | string) =>
      typeof item === 'object' ? item[this.selectionPrimaryKey] : item
    );

    this.selectedItemIds = selected
      ? union(this.selectedItemIds, itemIds)
      : without(this.selectedItemIds, ...itemIds);
    this.itemsSelect.emit(this.selectedItemIds);
  }

  changeSelectedItem(item: any) {
    this.selectedItemIds = [item[this.selectionPrimaryKey]];
    this.itemsSelect.emit(this.selectedItemIds);
  }

  cancel() {
    this.selectedItemIds = [];
    this.itemsSelect.emit(this.selectedItemIds);
  }

  isItemSelected(item) {
    return this.selectedItemIds.includes(item[this.selectionPrimaryKey]);
  }

  onColumnDrop({ previousIndex, currentIndex }) {
    const differentIndex = previousIndex !== currentIndex;

    if (differentIndex) {
      this.triggerEvent({
        action: PX_ACTIONS.REORDER_COLUMNS,
        column: this.columnNames[previousIndex]
      });
      const column = this.columns.splice(previousIndex, 1);
      this.columns.splice(currentIndex, 0, column[0]);
      this.emitConfigChange('reorderColumn');
    }

    this.updateColumnNames();
    this.updateGridColumnsSize();
  }

  updateGridColumnsSize() {
    this.styles = {
      ...this.styles,
      gridTemplateColumns: this.sanitizer.bypassSecurityTrustStyle(
        this.columns
          .filter(column => column.visible)
          .map(({ gridTrackSize }) => gridTrackSize)
          .join(' ')
      ),
      gridInfiniteScrollColumn: this.sanitizer.bypassSecurityTrustStyle(
        `1 / span ${this.columns.filter(column => column.visible).length}`
      )
    };
  }

  updateThEls() {
    setTimeout(() => {
      this.thEls = this.thRefs
        ? this.thRefs.toArray().map(({ nativeElement }) => nativeElement)
        : [];
    }, 0);
  }

  // To be removed when columns are transformed to observables.
  isDropDownPlacedRight(column: Column) {
    return (
      indexOf(
        this.columns.filter(c => c.visible),
        column
      ) >
      this.columns.filter(c => c.visible).length / 2
    );
  }

  emitConfigChange(eventType?: GridEventType) {
    if (this.columnsInitialized) {
      const columns = this.columns.map(this.mapColumnToConfig.bind(this));
      const config = { columns, pagination: this.pagination };
      this.onConfigChange.emit(config);
      switch (eventType) {
        case 'sort':
          this.onSort.emit(config);
          break;
        case 'pagination':
          this.onPageSizeChange.emit(config);
          break;
        case 'reorderColumn':
          this.onColumnReordered.emit(config);
          break;
        case 'changeColumnVisibility':
          this.onColumnVisibilityChange.emit(config);
      }
    }
  }

  triggerEvent(eventData) {
    this.gainsightService.triggerEvent(this.productExperienceEvent?.eventName || PX_EVENT_NAME, {
      ...this.productExperienceEvent?.data,
      ...eventData
    });
  }

  handleClick(row: Row) {
    this.lastClickedRow = row;
    this.rowClick.emit(row);
  }

  private onResetFilterAction(column: Column) {
    this.onColumnFilterReset.emit(column);
    return {
      filterPredicate: undefined,
      externalFilterQuery: undefined
    };
  }

  private mapColumnToConfig(column: Column): ColumnConfig | CustomColumnConfig {
    let config;
    if (column.custom) {
      const { visible, sortOrder, name, externalFilterQuery, header, path } = column;
      config = {
        visible,
        sortOrder,
        name,
        filter: { externalFilterQuery },
        header,
        path,
        custom: true
      };
    } else {
      const { visible, sortOrder, name, externalFilterQuery } = column;
      config = { visible, sortOrder, name, filter: { externalFilterQuery } };
    }

    if (isEmpty(config?.filter?.externalFilterQuery)) {
      delete config.filter;
    }

    return config;
  }

  private loadData(reload = false) {
    const {
      rows,
      columns,
      pagination,
      searchText,
      serverSideDataCallback,
      selectable,
      selectionPrimaryKey,
      infiniteScroll
    } = this;

    this.dataSource.loadData({
      rows,
      columns,
      pagination,
      searchText,
      serverSideDataCallback,
      selectable,
      selectionPrimaryKey,
      infiniteScroll,
      reload
    });
  }

  private updateColumns() {
    const specialColumn = {
      sortable: false,
      positionFixed: true
    };
    const selectionColumn = this.selectable
      ? {
          ...specialColumn,
          name: this.singleSelection ? SpecialColumnName.RadioButton : SpecialColumnName.Checkbox,
          gridTrackSize: '32px'
        }
      : undefined;
    const actionsColumn =
      this.actionControls?.length > 0
        ? {
            ...specialColumn,
            name: SpecialColumnName.Actions,
            gridTrackSize: this.actionControls.length > 2 ? '56px' : 'minmax(40px, auto)'
          }
        : undefined;

    const columns = [selectionColumn, ...this.columns, actionsColumn]
      .filter(Boolean)
      .map(this.withColumnDefaults);

    this.columns = uniqBy(columns, 'name');
    this.updateColumnNames();
    this.updateGridColumnsSize();
    this.updateThEls();
    this.updateFilteringApplied();
  }

  private checkIfAnyValuesExist(obj, results = []) {
    if (obj && Object.entries(obj)) {
      Object.entries(obj).forEach(([key, value]) => {
        if (typeof obj[key] === 'object') {
          this.checkIfAnyValuesExist(obj[key], results);
        } else {
          results.push(value);
        }
      });
    }

    return results.some(val => !!val);
  }

  private withColumnDefaults(column): Column {
    const dataType = column.dataType || ColumnDataType.TextShort;
    const { headerCSSClassName, cellCSSClassName } = column;

    return {
      visible: true,
      positionFixed: false,
      resizable: true,
      sortable: true,
      sortOrder: '',
      filterable: false,
      ...column,
      dataType,
      gridTrackSize:
        column.gridTrackSize ||
        `minmax(${minColumnGridTrackSize}px, ${ratiosByColumnTypes[dataType]}fr)`,
      headerCSSClassName:
        (typeof headerCSSClassName === 'string'
          ? headerCSSClassName.split(' ')
          : headerCSSClassName) || [],
      cellCSSClassName:
        (typeof cellCSSClassName === 'string' ? cellCSSClassName.split(' ') : cellCSSClassName) ||
        []
    };
  }

  private updateColumnNames() {
    this.columnNames = this.columns.map(({ name }) => name);
  }

  private setupResizeHandle() {
    const resizeHandleDrag$ = this.resizeHandleMouseDown$.pipe(
      takeUntil(this.unsubscribe$),
      tap(() => this.clearMouseHighlights()),
      mergeMap(({ event, targetColumnName }) => {
        this.columns = this.columns.map(column => {
          if (column.name === targetColumnName) {
            return {
              ...column,
              headerCSSClassName: union(column.headerCSSClassName, ['header--being-resized'])
            };
          }

          return column;
        });

        this.headerBeingResized = {
          columnName: targetColumnName,
          el: event.target?.parentNode
        };

        this.styles = {
          ...this.styles,
          tableCursor: 'col-resize'
        };

        return this.resizeHandleContainerMouseMove$.pipe(
          tap(() => this.clearMouseHighlights()),
          takeUntil(this.resizeHandleContainerMouseUp$)
        );
      })
    );

    resizeHandleDrag$.subscribe((event: MouseEvent) => {
      requestAnimationFrame(() => {
        this.columns = this.columns.map((column, i) => {
          if (this.headerBeingResized && column.name === this.headerBeingResized.columnName) {
            const scrollContainerDiv: HTMLDivElement = this.scrollContainer.nativeElement;

            // Read scrollContainerEl's offset left relative to the document.
            const horizontalOffset = scrollContainerDiv.getBoundingClientRect().left;

            // Adjust with the scrollContainerEl horizontal scroll position.
            const horizontalScrollOffset = scrollContainerDiv.scrollLeft - horizontalOffset;

            // Read left offset of the resized header.
            const headerOffsetLeft = this.headerBeingResized.el.offsetLeft || 0;

            // Calculate the desired width.
            const width = horizontalScrollOffset + event.clientX - headerOffsetLeft;

            return {
              ...column,
              // Update the column object with the new size value, enforce our minimum size.
              gridTrackSize: `${Math.max(minColumnGridTrackSize, width)}px`
            };
          }

          // For the other headers which don't have a set width, fix it to their computed width.
          if (column.gridTrackSize.startsWith('minmax')) {
            return {
              ...column,
              // isn't fixed yet (it would be a px value)
              gridTrackSize: `${_parseInt(this.thEls[i].clientWidth)}px`
            };
          }

          return column;
        });

        /*
         * Update the column sizes.
         * Note: grid-template-columns sets the width for all columns in one value.
         */
        this.updateGridColumnsSize();
      });
    });

    this.resizeHandleContainerMouseUp$.pipe(takeUntil(this.unsubscribe$)).subscribe(() => {
      if (this.headerBeingResized) {
        this.columns = this.columns.map(column => {
          if (column.name === this.headerBeingResized.columnName) {
            return {
              ...column,
              headerCSSClassName: without(column.headerCSSClassName, 'header--being-resized')
            };
          }

          return column;
        });
        this.headerBeingResized = undefined;

        this.styles = {
          ...this.styles,
          tableCursor: 'auto'
        };
      }
    });
  }

  private clearMouseHighlights() {
    if (window.getSelection) {
      window.getSelection().removeAllRanges();
    }
  }

  private _getCellRendererSpec({ type, value, row, columnName }): CellRendererSpec {
    const column = this.columns.find(({ name }) => name === columnName);
    const columnRenderer = this.getColumnRenderer(column);
    const rendererTemplate = get(
      columnRenderer,
      `${type === 'HEADER' ? 'headerCellRendererDef' : 'cellRendererDef'}.template`
    );

    const { cellRendererComponent, headerCellRendererComponent } = column;
    const rendererComponent =
      type === 'HEADER' ? headerCellRendererComponent : cellRendererComponent;

    return {
      renderer: rendererTemplate || rendererComponent,
      context: {
        value,
        item: row,
        property: column
      }
    };
  }

  private getColumnRenderer(column): ColumnDirective {
    return this.columnRenderers.toArray().find(({ name }) => name === column.name);
  }

  private updateFilteringLabelsParams(stats: DataSourceStats) {
    this.filteringLabelsParams = {
      filteredItemsCount: stats.filteredSize,
      allItemsCount: stats.size
    };
  }

  private updatePaginationLabelParams(stats: DataSourceStats) {
    if (stats.nextPage) {
      this.pagination = { ...this.pagination, nextPage: stats.nextPage };
    }

    const pageFirstItemIdx = (stats.currentPage - 1) * stats.firstPageSize + 1;
    this.paginationLabelParams = {
      pageFirstItemIdx,
      pageLastItemIdx: pageFirstItemIdx + (stats.currentPageSize - 1),
      itemsTotal: stats.filteredSize
    };
  }

  private createLoadMoreComponent(stats: DataSourceStats) {
    if (
      this.infiniteScroll &&
      stats &&
      stats.nextPage &&
      (!this.loadMoreComponent || this.recreateLoadMoreComponent)
    ) {
      this.recreateLoadMoreComponent = false;
      this.infiniteScrollContainer.clear();
      const componentRef = this.infiniteScrollContainer.createComponent(LoadMoreComponent);
      const instance = componentRef.instance as LoadMoreComponent;
      instance.useIntersection = this.infiniteScroll === 'auto' || this.infiniteScroll === 'hidden';
      instance.hidden = this.infiniteScroll === 'hidden';
      instance.paging = {
        nextPage: stats.nextPage,
        next: this.loadNextPage.bind(this)
      } as Paging<any>;
      instance.loadNextLabel = this.loadMoreItemsLabel;
      instance.loadingLabel = this.loadingItemsLabel;
      this.loadMoreComponent = instance;
    } else if (this.loadMoreComponent && !stats.nextPage) {
      this.loadMoreComponent.paging = {
        nextPage: null
      } as Paging<any>;
    }
  }

  private scrollToTop() {
    if (this.infiniteScroll) {
      this.scrollContainer.nativeElement.scrollTop = 0;
    }
  }

  private processAndPersistConfigChange() {
    merge(
      merge(
        this.onSort,
        this.onPageSizeChange,
        this.onColumnReordered,
        this.onColumnVisibilityChange
      ).pipe(map(config => config.columns)),
      merge(this.onAddCustomColumn, this.onRemoveCustomColumn).pipe(
        map(() => (this.columns || []).map(this.mapColumnToConfig.bind(this)))
      ),
      this.onFilter.pipe(
        map(({ columnName, filteringModifier }) =>
          this.columns.map(this.mapColumnToConfig.bind(this)).map((column: ColumnConfig) => {
            if (isNil(columnName)) {
              delete column.filter;
            } else if (column.name === columnName) {
              if (isEmpty(filteringModifier)) {
                delete column.filter;
              } else {
                column.filter = filteringModifier;
              }
            }

            return column;
          })
        )
      )
    )
      .pipe(
        map((columns: Array<ColumnConfig | CustomColumnConfig>) => ({
          columns,
          pagination: { pageSize: this.pagination.pageSize }
        })),
        filter(() => !!this.configurationStrategy),
        this.trimFilterConfigPipe(),
        this.trimSortConfigPipe(),
        this.trimCustomColumnConfigPipe(),
        this.ignoreColumnOrderPipe(),
        this.ignoreColumnVisibilityPipe(),
        concatMap((config: GridConfig) => this.configurationStrategy.saveConfig$(config)),
        takeUntil(this.unsubscribe$)
      )
      .subscribe();
  }

  private trimFilterConfigPipe(): UnaryFunction<Observable<GridConfig>, Observable<GridConfig>> {
    return pipe(
      this.checkEventPipe('filter', config => {
        config.columns = (config.columns || []).map(col => {
          delete col.filter;
          return col;
        });

        return config;
      })
    );
  }

  private trimSortConfigPipe(): UnaryFunction<Observable<GridConfig>, Observable<GridConfig>> {
    return pipe(
      this.checkEventPipe('sort', config => {
        config.columns = (config.columns || []).map(col => {
          col.sortOrder = '';
          return col;
        });

        return config;
      })
    );
  }

  private trimCustomColumnConfigPipe(): UnaryFunction<
    Observable<GridConfig>,
    Observable<GridConfig>
  > {
    return pipe(
      this.checkEventPipe('customColumns', config => {
        config.columns = (config.columns || []).filter((col: CustomColumnConfig) => !col.custom);

        return config;
      })
    );
  }

  private ignoreColumnOrderPipe(): UnaryFunction<Observable<GridConfig>, Observable<GridConfig>> {
    return pipe(
      this.checkEventPipe('order', config => {
        return this.configurationStrategy.getConfig$().pipe(
          map(oldConfig => {
            const oldColumns: ColumnConfig[] =
              oldConfig?.columns || (this.defaultColumns as ColumnConfig[]);
            // check if custom columns have been added
            const columnsAdded = (config.columns || []).filter(
              col => !oldColumns.find(old => old.name === col.name)
            );

            config.columns = [
              ...oldColumns.map(oldCol =>
                (config.columns || []).find(newCol => newCol.name === oldCol.name)
              ),
              ...columnsAdded
            ];

            return config;
          })
        );
      })
    );
  }

  private ignoreColumnVisibilityPipe(): UnaryFunction<
    Observable<GridConfig>,
    Observable<GridConfig>
  > {
    return pipe(
      this.checkEventPipe('visibility', config => {
        return this.configurationStrategy.getConfig$().pipe(
          map(oldConfig => {
            config.columns = (config.columns || []).map(newCol => {
              const columns: ColumnConfig[] =
                oldConfig?.columns || (this.defaultColumns as ColumnConfig[]);
              const oldCol = columns.find((col: any) => newCol.name === col.name);
              newCol.visible = oldCol?.visible ?? true;
              return newCol;
            });

            return config;
          })
        );
      })
    );
  }

  private checkEventPipe(
    configPart: GridConfigPart,
    trimEventDataFn: (config: GridConfig) => GridConfig | Observable<GridConfig>
  ): UnaryFunction<Observable<GridConfig>, Observable<GridConfig>> {
    return pipe(
      concatMap((config: GridConfig) => {
        return this.resolveConfigFilter
          .call(this, configPart)
          .pipe(map(keepEventData => ({ config, keepEventData })));
      }),
      map(({ config, keepEventData }) =>
        keepEventData ? config : trimEventDataFn.call(this, config)
      ),
      concatMap(config => (isObservable(config) ? config : of(config)) as Observable<GridConfig>)
    );
  }

  private resolveConfigFilter(configPart: GridConfigPart): Observable<boolean> {
    let result: boolean | Observable<boolean> | Promise<boolean>;
    const valueOrFn = this.configurationStrategy.getContext()?.configFilter?.[configPart];
    if (typeof valueOrFn === 'function') {
      result = valueOrFn();
    } else {
      result = valueOrFn;
    }
    return toObservable(result ?? true);
  }
}
