import { Inject, Injectable, Optional } from '@angular/core';
import { isEmpty } from 'lodash-es';
import { from, Observable, of } from 'rxjs';
import { UserPreferencesService } from '../common/user-preferences/user-preferences.service';
import { AbstractConfigurationStrategy } from './abstract-configuration-strategy';
import {
  DATA_GRID_CONFIGURATION_CONTEXT,
  DATA_GRID_CONFIGURATION_CONTEXT_PROVIDER,
  GridConfigContext,
  GridConfigContextProvider
} from './data-grid-configuration.model';
import { Column, GridConfig } from './data-grid.model';
import { LegacyGridConfigMapperService } from './legacy-grid-config-mapper.service';

export interface UserPreferencesGridConfigContext extends GridConfigContext {
  key: string;
  legacyConfigKey?: string;
  legacyFilterKey?: string;
  defaultColumns?: Column[];
}

/**
 * Stores data grid configuration in form of user preferences.
 */
@Injectable({ providedIn: 'root' })
export class UserPreferencesConfigurationStrategy extends AbstractConfigurationStrategy {
  constructor(
    protected userPreferencesService: UserPreferencesService,
    @Inject(DATA_GRID_CONFIGURATION_CONTEXT)
    @Optional()
    protected context: UserPreferencesGridConfigContext,
    @Inject(DATA_GRID_CONFIGURATION_CONTEXT_PROVIDER)
    @Optional()
    protected contextProvider: GridConfigContextProvider,
    protected legacyGridMapperService: LegacyGridConfigMapperService
  ) {
    super(context, contextProvider);
  }

  getConfig$(context?: UserPreferencesGridConfigContext): Observable<GridConfig> {
    const configContext: UserPreferencesGridConfigContext = this.retrieveContext(context);

    if (isEmpty(configContext)) {
      return of(undefined);
    } else {
      return configContext.legacyConfigKey || configContext.legacyFilterKey
        ? this.legacyGridMapperService.getMappedGridConfig(configContext)
        : this.userPreferencesService.get(configContext?.key);
    }
  }

  saveConfig$(
    config: GridConfig,
    context?: UserPreferencesGridConfigContext
  ): Observable<GridConfig> {
    const configContext: UserPreferencesGridConfigContext = this.retrieveContext(context);
    return isEmpty(configContext)
      ? of(undefined)
      : from(this.userPreferencesService.set(configContext?.key, config));
  }
}
