import {
  Component,
  ViewChild,
  Input,
  ViewContainerRef,
  AfterContentInit,
  OnDestroy,
  Injector,
  EnvironmentInjector
} from '@angular/core';
import { TabsService } from './tabs.service';
import { Tab, TabWithTemplate } from './tab.model';

@Component({
  selector: 'c8y-tab',
  template: '<ng-template #tpl><ng-content></ng-content></ng-template>'
})
export class TabComponent implements AfterContentInit, OnDestroy, TabWithTemplate {
  @ViewChild('tpl', { static: false }) template: ViewContainerRef;
  @Input() path: string;
  @Input() label = '';
  @Input() icon: string;
  @Input() priority: number;
  @Input() orientation: 'horizontal' | 'vertical';
  @Input() injector: Injector;

  constructor(private tabs: TabsService, _injector: EnvironmentInjector) {
    this.injector = _injector;
  }

  ngAfterContentInit() {
    setTimeout(() => this.tabs.add(this as Tab));
  }

  ngOnDestroy() {
    this.tabs.remove(this as Tab);
  }
}
