import { Pipe, PipeTransform } from '@angular/core';
import { IApplication } from '@c8y/client';
import { AppStateService } from '../common/ui-state.service';
import { Observable } from 'rxjs';
import { map, distinctUntilChanged } from 'rxjs/operators';
import { get } from 'lodash-es';

@Pipe({
  name: 'isActiveApp'
})
export class IsActiveAppPipe implements PipeTransform {
  constructor(private ui: AppStateService) {}

  transform(app: IApplication): Observable<boolean> {
    const { contextPath } = app;
    return this.ui.state$.pipe(
      map(state => get(state, 'app.contextPath')),
      distinctUntilChanged(),
      map(currentContextPath => contextPath === currentContextPath)
    );
  }
}
