import { Inject, Injectable, Optional } from '@angular/core';
import { AppStateService, ExtensionFactory } from '../common';
import { Version, VersionModuleConfig, VERSION_MODULE_CONFIG } from './version.model';

/**
 * Will provide the application version (taken from package.json's `version` field) and
 * the installed versions of the packages`@c8y/ng1-modules` and `@c8y/ngx-components` if available.
 */
@Injectable({
  providedIn: 'root'
})
export class WebSDKVersionFactory implements ExtensionFactory<Version> {
  webSDKVersions: Version[] = [];
  private defaults = {
    type: 'WebSDK',
    priority: 0,
    hidden: true
  };

  constructor(
    @Optional() @Inject(VERSION_MODULE_CONFIG) private config: VersionModuleConfig,
    private appState: AppStateService
  ) {
    if (this.config?.disableWebSDKVersionFactory) {
      return;
    }
    const { ng1, ngx, package: pkg } = this.appState.state.versions.ui;
    if (ng1) {
      this.webSDKVersions.push({
        ...this.defaults,
        label: '@c8y/ng1-modules',
        version: ng1
      });
    }
    if (ngx) {
      this.webSDKVersions.push({
        ...this.defaults,
        label: '@c8y/ngx-components',
        version: ngx
      });
    }
    if (pkg) {
      this.webSDKVersions.push({
        ...this.defaults,
        label: 'Frontend',
        version: pkg,
        hidden: false,
        priority: 50
      });
    }
  }

  get(): Version[] {
    return this.webSDKVersions;
  }
}
