import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  HostListener,
  Input,
  Output,
  ViewChild
} from '@angular/core';
import { Router } from '@angular/router';
import { IManagedObject, InventoryService, IResultList, Paging } from '@c8y/client';
import { gettext } from '../i18n/gettext';
import { BsDropdownDirective } from 'ngx-bootstrap/dropdown';
import { defer, empty, merge, Observable, of, Subject, UnaryFunction } from 'rxjs';
import { switchMap, takeUntil, tap } from 'rxjs/operators';
import { ManagedObjectType } from '../common/managed-object-type';
import { TypeaheadComponent } from '../select/typeahead.component';
import { InventorySearchService } from './inventory-search.service';

@Component({
  selector: 'c8y-search-input',
  templateUrl: './search-input.component.html'
})
export class SearchInputComponent {
  @Input()
  mode: 'search' | 'select' = 'search';

  /**
   * Unlocks the ability to place a custom template under the search input.
   */
  @Input() enableCustomTemplatePlaceholder = false;

  /**
   * A custom placeholder in the search bar.
   */
  @Input() customPlaceholder: string;

  /**
   * Event, which is used to set a new external term. Passing null will re-execute the query to BE.
   * This allows new filters to be applied to the currently selected term.
   */
  @Input() externalTerm: EventEmitter<string>;
  /**
   * A custom query setter used to override the standard query. In order to obtain data.
   */
  @Input()
  set customDataQuery(query: UnaryFunction<string, Observable<IResultList<IManagedObject>>>) {
    if (query) {
      this.customQuery = query;
    }
  }
  @Input() container: '' | 'body' = '';

  @Input() groupsOnly = false;

  @Output()
  filter = new EventEmitter<string>();

  @Output()
  search = new EventEmitter<string>();

  @Output()
  reset = new EventEmitter<IManagedObject>();

  @Output()
  onClick = new EventEmitter<IManagedObject>();

  deviceType: typeof ManagedObjectType = ManagedObjectType;
  term = '';
  defaultPlaceholder = gettext('Search for groups or assets');
  selected;

  customQuery: (text: string) => Observable<IResultList<IManagedObject>>;
  results$: Observable<IResultList<IManagedObject>>;
  recentSearchResults: IManagedObject[] = [];
  recentlyRegisteredResults$: Observable<IResultList<IManagedObject>>;
  isLoading = false;
  noMatch = false;

  private readonly RECENT_SEARCH_STORAGE_KEY = 'recent_search_view';
  private readonly MAX_RECENT_SEARCH_RESULTS = 5;
  private readonly DEFAULT_FILTER: object = {
    withTotalPages: true,
    pageSize: 5,
    withChildren: false
  };
  private readonly KEYCODE_ENTER = 'Enter';
  private readonly KEYCODE_ESC = 'Escape';
  private onDestroy$ = new Subject();

  @ViewChild(TypeaheadComponent, { static: false })
  private typeahead: TypeaheadComponent;

  @ViewChild('searchDropdown', { static: false })
  private dropdown: BsDropdownDirective;

  constructor(
    private router: Router,
    private inventory: InventoryService,
    private inventorySearchService: InventorySearchService,
    private cd: ChangeDetectorRef
  ) {}

  async ngOnInit() {
    const recentSearchIds = JSON.parse(localStorage.getItem(this.RECENT_SEARCH_STORAGE_KEY));
    if (recentSearchIds && recentSearchIds.length > 0) {
      const { data } = await this.inventory.list({ ids: recentSearchIds.join(',') });
      this.recentSearchResults = this.groupsOnly
        ? this.inventorySearchService.filterOnlyGroups(data)
        : data;
      this.recentlyRegisteredResults$ = defer(() =>
        this.inventory.list({
          q: '$orderby=creationTime desc',
          ...this.DEFAULT_FILTER
        })
      );
    }
    if (this.mode === 'select') {
      requestAnimationFrame(() => {
        this.subscribeOnSearch();
      });
    }

    if (this.externalTerm) {
      this.externalTerm.pipe(takeUntil(this.onDestroy$)).subscribe(term => {
        if (term === null) {
          this.typeahead.onSearch.emit(this.term);
          return;
        }
        this.typeahead.onSearch.emit(term);
      });
    }
  }

  @HostListener('document:keydown', ['$event']) onKeydownHandler(event: KeyboardEvent) {
    if (event.key === this.KEYCODE_ESC) {
      this.hideDropdown();
    }
  }

  onOpenChange(isOpen: boolean): void {
    if (isOpen) {
      // needs to request an animation frame as
      // otherwise the typeahead is undefined
      requestAnimationFrame(() => {
        this.subscribeOnSearch();
        this.typeahead.dropdown.show();
        this.typeahead.searchControl.nativeElement.focus();
      });
    }
  }

  open(event: Event, mo: IManagedObject, term?) {
    event.stopPropagation();
    const isAlreadyRecent = this.recentSearchResults.find(({ id }) => id === mo.id);
    if (!isAlreadyRecent) {
      this.recentSearchResults.unshift(mo);
      this.recentSearchResults = this.recentSearchResults.slice(0, this.MAX_RECENT_SEARCH_RESULTS);
    }
    const recentSearchIds = this.recentSearchResults.map(({ id }) => id);
    localStorage.setItem(this.RECENT_SEARCH_STORAGE_KEY, JSON.stringify(recentSearchIds));
    if (term) {
      this.selected = mo;
      this.term = term;
    }
    this.onClick.emit(mo);
    this.hideDropdown();
  }

  onReset(status: { icon: string; $event: MouseEvent }) {
    status.$event.stopPropagation();
    this.reset.emit(this.selected);
    this.typeahead.onSearch.emit('');
    this.selected = undefined;
    this.typeahead.searchControl.nativeElement.focus();
  }

  keyDown(event: KeyboardEvent) {
    if (event.key === this.KEYCODE_ENTER) {
      // enter hit can be faster then typeahead debounce,
      // therefore we take the term from the DOM element
      // itself:
      const searchTerm = (event.target as any).value;
      this.onSearch(searchTerm);
    }
  }

  onSearch(search: string) {
    this.search.emit(search);
    this.hideDropdown();
  }

  onFilter(search: string) {
    this.filter.emit(search);
    this.hideDropdown();
  }

  onOpenAssetTable() {
    this.router.navigateByUrl('/assetsearch');
    this.hideDropdown();
  }

  ngOnDestroy() {
    if (this.onDestroy$) {
      this.onDestroy$.next();
      this.onDestroy$.complete();
    }
  }

  private hideDropdown() {
    if (this.dropdown) {
      this.dropdown.hide();
      return;
    }

    if (this.typeahead && this.typeahead.dropdown) {
      this.typeahead.dropdown.hide();
      return;
    }
  }

  private subscribeOnSearch() {
    if (!this.results$) {
      this.results$ = this.typeahead.onSearch.pipe(
        tap(term => this.onTypingStarted(term)),
        switchMap(term => this.mergeRequest(term)),
        tap(() => this.cd.detectChanges())
      );
    }
  }

  private mergeRequest(term: string): Observable<IResultList<IManagedObject>> {
    return merge(
      of({ data: [] } as IResultList<IManagedObject>),
      this.handleQuery(term).pipe(tap(({ data, paging }) => this.onLoadingDone(data, paging)))
    );
  }

  private handleQuery(term: string) {
    if (!term) {
      return empty();
    }
    if (this.customQuery) {
      return defer(() => this.customQuery(term));
    }
    return defer(() =>
      this.groupsOnly
        ? this.inventorySearchService.searchGroups(term)
        : this.inventorySearchService.search(term)
    );
  }

  private onLoadingDone(data: IManagedObject[], paging: Paging<IManagedObject>) {
    this.isLoading = false;
    this.noMatch = paging && paging.nextPage === null && data.length === 0;
  }

  private onTypingStarted(term) {
    this.noMatch = false;
    this.term = term;
    this.isLoading = term.length > 0;
  }
}
