import { Injectable } from '@angular/core';
import { IApplication, ICurrentTenant } from '@c8y/client';
import { get, orderBy } from 'lodash-es';
import { combineLatest, Observable } from 'rxjs';
import { map, shareReplay, take } from 'rxjs/operators';
import { AppStateService } from '../common';

@Injectable({ providedIn: 'root' })
export class AppSwitcherService {
  /**
   * Apps of the currently logged in user.
   */
  appsOfCurrentUser$: Observable<Array<IApplication>>;
  /**
   * Available cloud apps of the currently logged in user to be displayed in app switcher.
   */
  oneCloudApps$: Observable<Array<IApplication>>;
  /**
   * Available none cloud apps of the currently logged in user to be displayed in app switcher.
   */
  apps$: Observable<Array<IApplication>>;
  finishedLoading$: Observable<boolean>;

  protected readonly visibleApplicationTypes = ['HOSTED', 'EXTERNAL'];

  constructor(protected ui: AppStateService) {
    const { currentTenant } = this.ui;
    this.appsOfCurrentUser$ = this.ui.currentAppsOfUser.pipe(
      map(apps => this.filterVisible(apps)),
      shareReplay(1)
    );
    this.oneCloudApps$ = this.appsOfCurrentUser$.pipe(
      map(apps => apps.filter(app => this.isCloudApp(app))),
      map(cloudApps => this.orderApps(cloudApps)),
      shareReplay(1)
    );
    const nonCloudApps$ = this.appsOfCurrentUser$.pipe(
      map(apps => apps.filter(app => !this.isCloudApp(app)))
    );
    this.apps$ = combineLatest([nonCloudApps$, currentTenant]).pipe(
      map(([apps, tenant]) => this.filterDuplicates(apps, tenant)),
      map(apps => this.orderApps(apps)),
      shareReplay(1)
    );

    this.finishedLoading$ = combineLatest([this.apps$, this.oneCloudApps$]).pipe(
      map(() => true),
      take(1),
      shareReplay(1)
    );
  }

  protected filterVisible(apps: IApplication[]): IApplication[] {
    return apps.filter(
      app =>
        this.visibleApplicationTypes.includes(app.type) &&
        !app.noAppSwitcher &&
        !get(app, 'manifest.noAppSwitcher')
    );
  }

  protected isCloudApp(app: IApplication): boolean {
    return !!app.cloud;
  }

  protected filterDuplicates(apps: IApplication[], tenant: ICurrentTenant): IApplication[] {
    // Filter out apps that have duplicate contextpaths and are not own owned by the current tenant;
    const filterFn = app =>
      !apps.some(
        otherApp =>
          app !== otherApp &&
          app.contextPath &&
          app.contextPath === otherApp.contextPath &&
          app.owner.tenant.id !== tenant.name
      );
    return apps.filter(filterFn);
  }

  protected orderApps(apps: IApplication[]): IApplication[] {
    return orderBy(apps, ({ name }) => name.toLowerCase());
  }
}
