import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AppStateService } from '../../common/ui-state.service';
import { HeaderService } from '../../header/header.service';
import { PRODUCT_EXPERIENCE } from '../../shared/core.model';
import { legalNoticesItem } from '../defaults.items';
import { DocLink } from '../docs.models';
import { DocsService } from '../docs.service';

@Component({
  selector: 'c8y-help-and-support-outlet',
  templateUrl: './help-and-support-outlet.component.html'
})
export class HelpAndSupportOutletComponent {
  PRODUCT_EXPERIENCE = PRODUCT_EXPERIENCE;
  helpAndSupport$: Observable<DocLink[]>;
  documentationOpen = false;
  appState$: Observable<any>;
  drawerOpen$: Observable<boolean>;

  constructor(
    private docsService: DocsService,
    private appState: AppStateService,
    private headerService: HeaderService
  ) {
    this.helpAndSupport$ = this.docsService.items$.pipe(
      map(items =>
        items.filter(item => item.type === 'doc' && item.label !== legalNoticesItem.label)
      )
    );
    this.appState$ = this.appState.state$;
    this.drawerOpen$ = this.headerService.rightDrawerOpen$;
  }
}
