import { Component, OnDestroy, OnInit } from '@angular/core';
import { AppStateService } from '../common/ui-state.service';
import { TranslateService } from '../i18n/translate.service';
import { ModalService } from '../modal/modal.service';
import { gettext } from '../i18n/gettext';
import { UserPreferencesService } from '../common/user-preferences/user-preferences.service';
import { Status } from '../common/status.model';
import { Observable, Subject } from 'rxjs';
import { HeaderService } from '../header/header.service';
import { filter, first, takeUntil } from 'rxjs/operators';

@Component({
  selector: 'c8y-ui-settings',
  templateUrl: './ui-settings.component.html'
})
export class UiSettingsComponent implements OnInit, OnDestroy {
  open$: Observable<boolean>;
  currentLang: string;
  languages: { lang: string; nativeLanguage: string }[];
  private destroyed$: Subject<void> = new Subject();

  constructor(
    private translate: TranslateService,
    private state: AppStateService,
    private ui: AppStateService,
    private userPreferences: UserPreferencesService,
    private c8yModalService: ModalService,
    private headerService: HeaderService
  ) {
    this.currentLang = this.ui.state.lang;
    this.ui.state$
      .pipe(
        filter(({ lang }) => lang !== this.currentLang),
        takeUntil(this.destroyed$),
        first()
      )
      .subscribe(({ lang }) => (this.currentLang = lang));
    this.open$ = this.headerService.rightDrawerOpen$;
  }

  ngOnInit() {
    this.languages = (this.state.state.langs as string[]).map(l => ({
      lang: l,
      nativeLanguage: this.translate.getNativeLanguage(l)
    }));
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

  async onLanguageChange(changedLang: string): Promise<void> {
    if (!changedLang) {
      return;
    }
    let reloadRequired = false;
    await this.translate.switchToLanguage(changedLang);
    if (changedLang !== this.currentLang) {
      reloadRequired = await this.persistLanguage(changedLang);
    }
    if (reloadRequired) {
      location.reload();
    }
  }

  async persistLanguage(lang: string): Promise<boolean> {
    let shouldReload: boolean | PromiseLike<boolean>;
    try {
      await this.c8yModalService.confirm(
        gettext('Reload recommended'),
        gettext(
          'To change the language in the entire application, we recommend you to reload the page. If you have any unsaved changes, you can reload later. How would you like to proceed?'
        ),
        Status.WARNING,
        {
          ok: gettext('Reload now'),
          cancel: gettext('Reload later')
        }
      );
      this.translate.saveInLocalStorage(lang);
      await this.userPreferences.set('language', lang);
      this.currentLang = lang;
      shouldReload = true;
    } catch (ex) {
      this.translate.saveInLocalStorage(lang);
      await this.userPreferences.set('language', lang);
      this.currentLang = lang;
      shouldReload = false;
    }

    return shouldReload;
  }
}
