import { Inject, Injectable, Optional } from '@angular/core';
import { AppStateService, ExtensionFactory } from '../common';
import { Version, VersionModuleConfig, VERSION_MODULE_CONFIG } from './version.model';
import { Observable, of } from 'rxjs';
import { distinctUntilChanged, filter, map } from 'rxjs/operators';
import { PluginsService } from '../plugins';
import { ApplicationRemotePlugins } from '@c8y/client';

/**
 * Will provide the version of plugins installed to the current application.
 */
@Injectable({
  providedIn: 'root'
})
export class WebSDKPluginVersionFactory implements ExtensionFactory<Version> {
  webSDKPluginVersions$: Observable<Version[]> = of([]);

  constructor(
    @Optional() @Inject(VERSION_MODULE_CONFIG) private config: VersionModuleConfig,
    private appState: AppStateService,
    private plugins: PluginsService
  ) {
    if (this.config?.disableWebSDKPluginVersionFactory) {
      return;
    }
    this.webSDKPluginVersions$ = this.appState.currentApplication.pipe(
      map(app => this.plugins.getMFRemotes(app)),
      filter(remotes => !!remotes),
      distinctUntilChanged(),
      map(remotes => this.getVersionsFromRemotes(remotes))
    );
  }

  get(): Observable<Version[]> {
    return this.webSDKPluginVersions$;
  }

  getVersionsFromRemotes(remotes: ApplicationRemotePlugins): Version[] {
    const versions = new Array<Version>();
    for (const key of Object.keys(remotes)) {
      const modules = remotes[key];
      if (!modules?.length) {
        continue;
      }
      const [contextPath, version] = key.split('@', 2);
      if (!version) {
        continue;
      }

      const versionsBehindKey: Version[] = modules.map(module => {
        return {
          label: `${contextPath} => ${module}`,
          version,
          type: 'INSTALLED_MF_PLUGINS',
          hideInRightDrawer: true,
          custom: {
            key,
            contextPath,
            module
          }
        };
      });
      versions.push(...versionsBehindKey);
    }
    return versions;
  }
}
