import { Injectable } from '@angular/core';
import { ExtensionFactory } from '../common/extension-hooks';
import { gettext } from '../i18n/gettext';
import { Step, Steppers } from '../stepper/stepper.model';
import { SetupCompletedComponent } from './setup-completed.component';
import { SetupStep, SETUP_FINISHED_STEP_ID } from './setup.model';

/**
 * A factory providing the 'Summary' step to the setup stepper.
 */
@Injectable({ providedIn: 'root' })
export class SetupStepperFactory implements ExtensionFactory<Step> {
  private readonly step = {
    setupId: SETUP_FINISHED_STEP_ID,
    stepperId: Steppers.SETUP,
    component: SetupCompletedComponent,
    label: gettext('Summary'),
    priority: -Infinity
  } as SetupStep;

  get(): SetupStep {
    return this.step;
  }
}
