import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { distinctUntilChanged, shareReplay } from 'rxjs/operators';
import {
  fromTrigger,
  GenericHookType,
  hookGeneric,
  getInjectedHooks,
  stateToFactory,
  ExtensionPointForPlugins,
  HookOptions
} from '../common/extension-hooks';
import { PluginsResolveService } from '../plugins';
import { Search } from './search.model';

/**
 * A hook to use for Multi Provider extension.
 * Consider using the `hookSearch` function instead.
 * @deprecated Please don't use the search factory. It will be refactored soon.
 */
export const HOOK_SEARCH = new InjectionToken('HOOK_SEARCH');

/**
 * You can either provide a single `Search` as parameter:
 * ```typescript
 *  hookSearch(...)
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  hookSearch([...])
 * ```
 *
 * Or you provide an Service that implements `ExtensionFactory<Search>`
 * ```typescript
 *  export class MySearchFactory implements ExtensionFactory<Search> {...}
 *  ...
 *  hookSearch(MySearchFactory)
 * ```
 * A typed alternative to `HOOK_SEARCH`.
 * @param search The `Search`'s or `ExtensionFactory` to be provided.
 * @returns An `Provider` to be provided in your module.
 * @deprecated Please don't use the search factory. It will be refactored soon.
 */
export function hookSearch(search: GenericHookType<Search>, options?: Partial<HookOptions>) {
  return hookGeneric<Search>(search, HOOK_SEARCH, options);
}

export enum SearchFilters {
  ONLY_DEVICES = 'onlyDevices',
  ONLY_GROUPS_AND_ASSETS = 'onlyGroupsAndAssets',
  ALL_FILTERS = 'allFilters'
}

/**
 * A service which defines the search function.
 */
@Injectable({
  providedIn: 'root'
})
export class SearchService extends ExtensionPointForPlugins<Search> {
  constructor(rootInjector: Injector, private router: Router, plugins: PluginsResolveService) {
    super(rootInjector, plugins);
    this.items$ = this.setupItemsObservable();
  }

  /**
   * Returns the current state.
   * @readonly
   * @returns The current set of actions.
   */
  get state() {
    return this.state$.value;
  }

  /**
   * Adds a new item to the search in the header.
   * @param item The item to add.
   */
  add(item: Search) {
    this.state.add(item);
    this.emitNewState();
  }

  /**
   * Removes an search from the header.
   * @param item The item to remove.
   */
  remove(item: Search) {
    this.state.delete(item);
    this.emitNewState();
  }

  protected setupItemsObservable(): Observable<Search[]> {
    return fromTrigger<Search>(this.router, this.refresh$, [
      getInjectedHooks<Search>(HOOK_SEARCH, this.injectors),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(shareReplay(1), distinctUntilChanged());
  }
}
