import { Pipe, PipeTransform } from '@angular/core';
import { ISelectModalObject } from './select-modal.model';

@Pipe({
  name: 'selectModalFilterPipe'
})
export class SelectModalFilterPipe implements PipeTransform {
  transform(items: ISelectModalObject[], filterTerm: string): any {
    return filterTerm.trim().length === 0
      ? items
      : items.filter((item: ISelectModalObject) => {
          const allValues = this.extractValues(item);
          const filteredValues = allValues.filter(value =>
            this.filterContainString(value, filterTerm)
          );
          return filteredValues.length > 0;
        });
  }

  private filterContainString(value: string, filterTerm: string) {
    const term = filterTerm.toLowerCase().trim();
    return value && value.toLowerCase().indexOf(term) > -1;
  }

  private extractValues(item) {
    let values = [];
    if (item.value) {
      values.push(item.value);
    }
    Object.keys(item).forEach(key => {
      if (Array.isArray(item[key])) {
        item[key].forEach(obj => values.concat(this.extractValues(obj)));
      }
      if (item[key] instanceof Object) {
        values = values.concat(this.extractValues(item[key]));
      }
    });
    return values;
  }
}
