import { Component, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { gettext } from '../../../i18n';

@Component({
  selector: 'c8y-realtime-control',
  templateUrl: './realtime-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RealtimeControlComponent),
      multi: true
    }
  ]
})
export class RealtimeControlComponent implements ControlValueAccessor {
  readonly disableRealtimeLabel = gettext('Disable realtime');
  readonly enableRealtimeLabel = gettext('Enable realtime');

  value: boolean;
  touched = false;
  disabled = false;

  // eslint-disable-next-line @typescript-eslint/no-empty-function
  onChange = _ => {};
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  onTouched = () => {};

  writeValue(value: boolean): void {
    this.value = value;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(onTouched: any): void {
    this.onTouched = onTouched;
  }

  markAsTouched(): void {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean): void {
    this.disabled = disabled;
  }
}
