import { Injectable } from '@angular/core';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { get } from 'lodash-es';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { DatePipe } from '../../common';
import { toObservable } from '../../common/extension-hooks';
import { FilterChip, FilterMapper } from '../data-grid.model';

@Injectable({ providedIn: 'root' })
export class BooleanFilterMapper implements FilterMapper {
  fieldType = ['switch', 'boolean', 'checkbox'];
  map(field: FormlyFieldConfig, filter: FilterChip): Observable<FilterChip> {
    if (this.fieldType.includes(field.type.toString())) {
      if (get(filter.externalFilterQuery, filter.path)) {
        return of({
          ...filter,
          value: get(filter.externalFilterQuery, filter.path),
          displayValue: field.props?.label
        });
      }
    }
  }
}

@Injectable({ providedIn: 'root' })
export class RadioFilterMapper implements FilterMapper {
  fieldType = ['radio', 'enum'];
  map(field: FormlyFieldConfig, filter: FilterChip) {
    if (this.fieldType.includes(field.type.toString())) {
      const fieldOptions = field.props?.options || field.props?.enum;
      return toObservable(fieldOptions).pipe(
        map(options => {
          const object =
            options.find(option => option.value === get(filter.externalFilterQuery, filter.path)) ??
            options.find(option => option === get(filter.externalFilterQuery, filter.path));
          return {
            ...filter,
            value: object,
            displayValue: object?.label
          };
        })
      );
    }
  }
}

@Injectable({ providedIn: 'root' })
export class TypeaheadFilterMapper implements FilterMapper {
  fieldType = 'typeahead';
  map(field: FormlyFieldConfig, filter: FilterChip) {
    if (this.fieldType === field.type) {
      const displayProperty = field.props?.displayProperty;

      const valueNoProps = get(filter.externalFilterQuery, filter.path);
      const valueProperties = field.props?.valueProperties;

      let value = valueNoProps;
      if (valueProperties) {
        value = valueProperties.reduce((acc, prop) => {
          const value = get(filter.externalFilterQuery, [...filter.path, prop]);
          return { ...acc, [prop]: value };
        }, {});
      }

      return of({
        ...filter,
        value,
        displayValue: get(filter.externalFilterQuery, [...filter.path, displayProperty])
      });
    }
  }
}

@Injectable({ providedIn: 'root' })
export class DateFilterMapper implements FilterMapper {
  fieldType = ['date-time', 'date'];

  constructor(private datePipe: DatePipe) {}

  map(field: FormlyFieldConfig, filter: FilterChip) {
    if (this.fieldType.includes(field.type.toString())) {
      return of({
        ...filter,
        label: field.props?.label,
        displayValue: this.datePipe
          .transform(new Date(get(filter.externalFilterQuery, filter.path)))
          .toString(),
        value: get(filter.externalFilterQuery, filter.path)
      });
    }
  }
}

@Injectable({ providedIn: 'root' })
export class SelectFilterMapper implements FilterMapper {
  fieldType = 'select';
  map(field: FormlyFieldConfig, filter: FilterChip) {
    if (this.fieldType === field.type) {
      return toObservable(field.props?.options).pipe(
        map(options => {
          const selectedOption = options.find(
            option => option.value === get(filter.externalFilterQuery, filter.path)
          );
          return {
            ...filter,
            value: get(filter.externalFilterQuery, filter.path),
            displayValue: selectedOption.label
          };
        })
      );
    }
  }
}

@Injectable({ providedIn: 'root' })
export class StringFilterMapper implements FilterMapper {
  fieldType = ['string', 'input'];
  map(field: FormlyFieldConfig, filter: FilterChip): Observable<FilterChip> {
    if (this.fieldType.includes(field.type.toString())) {
      if (get(filter.externalFilterQuery, filter.path)) {
        return of({
          ...filter,
          value: get(filter.externalFilterQuery, filter.path),
          displayValue: get(filter.externalFilterQuery, filter.path)
        });
      }
    }
  }
}
