import { Injectable } from '@angular/core';
import { ExtensionFactory } from '../common';
import { DrawerItem } from '../drawer';
import { NavigatorOutletComponent } from './navigator-outlet.component';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { NavigatorService } from './navigator.service';

@Injectable()
export class NavigatorDrawerFactory implements ExtensionFactory<DrawerItem> {
  protected drawerItem: DrawerItem = {
    component: NavigatorOutletComponent,
    position: 'left',
    priority: 50,
    id: 'navigatorOutlet'
  };

  constructor(private navigatorService: NavigatorService) {}

  get(): Observable<DrawerItem | DrawerItem[]> {
    return this.navigatorService.hasItemsInNavigator$.pipe(
      map(tmp => (tmp ? this.drawerItem : []))
    );
  }
}
