import { Component, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Interval, INTERVALS } from '../widget-time-context.model';

@Component({
  selector: 'c8y-interval-picker',
  templateUrl: './interval-picker.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => IntervalPickerComponent),
      multi: true
    }
  ]
})
export class IntervalPickerComponent implements ControlValueAccessor {
  INTERVALS = INTERVALS;

  value: Interval['id'];
  touched = false;
  disabled = false;

  // eslint-disable-next-line @typescript-eslint/no-empty-function
  onChange = _ => {};
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  onTouched = () => {};

  writeValue(value: Interval['id']) {
    this.value = value;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }
}
