import { Component, Input, Output, ViewChild, EventEmitter } from '@angular/core';
import { Search } from './search.model';
import { SearchService } from './search.service';

@Component({
  selector: 'c8y-search',
  templateUrl: './search.component.html'
})
export class SearchComponent implements Search {
  @Input() name: string;
  @Input() icon = 'search';
  @Input() priority = 0;
  @ViewChild('template', { static: false }) template: any;
  @Output() search = new EventEmitter<Search>();
  @Input() term = '';

  private viewInitTimeout;

  constructor(private searchService: SearchService) {}

  onSearch() {
    this.search.emit(this);
  }

  ngAfterViewInit() {
    this.template = undefined;
    this.viewInitTimeout = setTimeout(() => this.searchService.add(this as Search));
  }

  ngOnDestroy() {
    clearTimeout(this.viewInitTimeout);
    this.searchService.remove(this as Search);
  }
}
