import { gettext } from '../i18n';
import { Injectable } from '@angular/core';
import { AlertService } from '../alert/alert.service';

@Injectable({
  providedIn: 'root'
})
export class ClipboardService {
  constructor(private alertService: AlertService) {}

  async writeText(text: string) {
    try {
      await navigator.clipboard.writeText(text);
      this.alertService.success(gettext('Copied to clipboard.'));
    } catch (e) {
      this.alertService.danger(gettext('Could not copy to clipboard.'));
    }
  }

  async readText(): Promise<string> {
    try {
      const text = await navigator.clipboard.readText();
      this.alertService.success(gettext('Pasted from clipboard.'));
      return text;
    } catch (e) {
      this.alertService.danger(gettext('Could not paste from clipboard.'));
    }
  }
}
