const cache = new Map();
/**
 * @ignore
 */
export function loadLocale(moduleLang: string) {
  return new Promise((resolve, reject) => {
    if (cache.has(moduleLang)) {
      resolve(cache.get(moduleLang));
    }

    try {
      import(
        /* webpackMode: "lazy" */
        `/node_modules/@angular/common/locales/${moduleLang}.mjs`
      ).then(module => {
        cache.set(moduleLang, module);
        resolve(module);
      });
    } catch (error) {
      reject(error);
    }
  });
}
