import { Injectable } from '@angular/core';
import { ExtensionFactory, OptionsService } from '../../common';
import { DrawerItem } from '../../drawer';
import { NavigatorBottomComponent } from './navigator-bottom.component';

@Injectable()
export class NavigatorBottomDrawerFactory implements ExtensionFactory<DrawerItem> {
  protected drawerItem: DrawerItem = {
    component: NavigatorBottomComponent,
    position: 'left',
    priority: 1001,
    id: 'poweredBy',
    noneRequired: true
  };

  constructor(private options: OptionsService) {}

  get(): DrawerItem[] | DrawerItem {
    if (this.options.get('hidePowered')) {
      return [];
    }
    return this.drawerItem;
  }
}
