import { Component, OnInit } from '@angular/core';
import {
  ConfigOption,
  FieldArrayType,
  FormlyFieldConfig,
  FormlyFieldProps
} from '@ngx-formly/core';

@Component({
  selector: 'c8y-array-type',
  templateUrl: './array.type.component.html'
})
export class ArrayTypeComponent extends FieldArrayType implements OnInit {
  static readonly CONFIG: ConfigOption = {
    types: [{ name: 'array', component: ArrayTypeComponent }]
  };

  fieldArrayProps: FormlyFieldProps;

  add() {
    this.formControl.markAsTouched();
    super.add();
  }

  remove(i: number) {
    this.formControl.markAsTouched();
    super.remove(i);
  }

  ngOnInit(): void {
    this.fieldArrayProps =
      typeof this.field?.fieldArray === 'function'
        ? (this.field.fieldArray as (field: FormlyFieldConfig) => FormlyFieldConfig)(this.field)
            ?.props
        : this.field?.fieldArray?.props;
    if (this.props.required && !this.field.fieldGroup.length) {
      this.add();
    }
  }
}
