import { Directive } from '@angular/core';
import {
  AbstractControl,
  NG_VALIDATORS,
  ValidationErrors,
  Validator,
  Validators
} from '@angular/forms';

const SIMPLE_JSON_PATH_REGEX = /^[^.\s]+(\.\S+)*$/;

export function simpleJsonPathValidator(control: AbstractControl): ValidationErrors | null {
  const errors = Validators.pattern(SIMPLE_JSON_PATH_REGEX)(control);

  return !!errors
    ? {
        simpleJsonPath: errors.pattern
      }
    : null;
}

@Directive({
  selector: '[simpleJsonPath]',
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: SimpleJsonPathValidatorDirective,
      multi: true
    }
  ]
})
export class SimpleJsonPathValidatorDirective implements Validator {
  validate(control: AbstractControl<any, any>): ValidationErrors | null {
    return simpleJsonPathValidator(control);
  }
}
