import { Directive, Input, HostListener } from '@angular/core';

/**
 * Sets the focus on the give targetId on click.
 */
@Directive({
  selector: '[c8ySkipLink]'
})
export class SkipLinkDirective {
  @Input('c8ySkipLink') targetId: string;

  @HostListener('click')
  onClick() {
    const element = document.getElementById(this.targetId);
    if (element) {
      element.focus();
    }
  }
}
