import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { distinctUntilChanged, filter, map, switchMap } from 'rxjs/operators';
import { DrawerItem, DrawerPositions } from '../drawer.model';
import { DrawerService } from '../drawer.service';

@Component({
  selector: 'c8y-drawer-outlet',
  templateUrl: './drawer-outlet.component.html'
})
export class DrawerOutletComponent implements OnChanges {
  @Input() position: DrawerPositions;
  @Input() open: boolean;
  drawerItems$: Observable<DrawerItem[]>;

  isLeft$: Observable<boolean>;

  private drawerPosition$ = new BehaviorSubject<DrawerPositions>(null);

  constructor(private drawerService: DrawerService) {
    const cleanedPosition$ = this.drawerPosition$.pipe(
      filter(p => !!p),
      distinctUntilChanged()
    );
    this.isLeft$ = cleanedPosition$.pipe(map(position => position === 'left'));
    this.drawerItems$ = cleanedPosition$.pipe(
      filter(p => !!p),
      distinctUntilChanged(),
      switchMap(position => this.drawerService.getDrawersForPosition$(position))
    );
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.position) {
      this.drawerPosition$.next(this.position);
    }
  }
}
