import { Pipe, PipeTransform, TemplateRef } from '@angular/core';
import { FormlyFieldConfig } from '@ngx-formly/core';

/**
 * Injects custom template to field config, so it can be rendered inside form-field-wrapper along an input.
 */
@Pipe({
  name: 'c8yArrayTypeInjectTemplate'
})
export class C8yArrayTypeInjectTemplatePipe implements PipeTransform {
  transform(field: FormlyFieldConfig, customTemplateRef: TemplateRef<any>): FormlyFieldConfig {
    field.templateOptions.customTemplateRef = customTemplateRef;
    return field;
  }
}
