import { Directive, Input } from '@angular/core';
import {
  AbstractControl,
  NG_VALIDATORS,
  ValidationErrors,
  Validator,
  ValidatorFn
} from '@angular/forms';
import { find, get, isEqualWith } from 'lodash-es';

export function uniqueInCollectionByPathValidator(
  collection: Iterable<object>,
  path: string,
  comparator?: (val1: any, val2: any) => boolean
): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    const match = find(collection, element =>
      isEqualWith(get(element, path), control.value, comparator)
    );

    return !!match && !!control.value
      ? { uniqueByPath: { key: path, value: get(match, path) } }
      : null;
  };
}

@Directive({
  selector: '[uniqueByPath]',
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: UniqueInCollectionByPathValidationDirective,
      multi: true
    }
  ]
})
export class UniqueInCollectionByPathValidationDirective implements Validator {
  @Input() collection: Iterable<object>;
  @Input() path: string;
  @Input() comparator: (val1: any, val2: any) => boolean;

  validate(control: AbstractControl): ValidationErrors | null {
    return uniqueInCollectionByPathValidator(this.collection, this.path, this.comparator)(control);
  }
}
