import {
  ClassProvider,
  ExistingProvider,
  InjectionToken,
  Injector,
  Type,
  ValueProvider
} from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { castArray, flatten, groupBy, sortBy, uniq } from 'lodash-es';
import {
  BehaviorSubject,
  combineLatest,
  defer,
  from,
  isObservable,
  merge,
  Observable,
  of,
  race,
  Subject
} from 'rxjs';
import { filter, map, startWith, switchMap } from 'rxjs/operators';
import { PluginsResolveService } from '../plugins';
import { StateService } from './state-service.abstract';

export function fromTrigger<T>(
  router: Router,
  refresh: Observable<any> | Observable<any>[],
  factories: Array<
    | T
    | T[]
    | ExtensionFactory<T>
    | ExtensionFactory<T>[]
    | (() => T | ExtensionFactory<T> | Array<T | ExtensionFactory<T>>)
  >
): Observable<T[]> {
  return merge(
    router.events.pipe(filter(evt => evt instanceof NavigationEnd)),
    ...castArray(refresh)
  ).pipe(
    startWith(1),
    switchMap(() => fromFactories<T>(factories, router))
  );
}

export function fromTriggerOnce<T>(
  router: Router,
  refresh: Observable<any> | Observable<any>[],
  factories: Array<
    | T
    | T[]
    | ExtensionFactory<T>
    | ExtensionFactory<T>[]
    | (() => T | ExtensionFactory<T> | Array<T | ExtensionFactory<T>>)
  >
): Observable<T[]> {
  return merge(...castArray(refresh)).pipe(
    startWith(1),
    switchMap(() => fromFactories<T>(factories, router))
  );
}

export enum InjectionType {
  COMPONENT,
  ROUTE
}

export function getInjectedHooks<T>(
  token: InjectionToken<T[]>,
  injectors: Injector[],
  type = InjectionType.COMPONENT
): () => T[] {
  return () =>
    flatten(
      injectors.map(injector => {
        const factoryOrFactories = injector.get<T | T[]>(token, [], { self: true });
        const factories: T[] = Array.isArray(factoryOrFactories)
          ? flatten(factoryOrFactories)
          : [factoryOrFactories];
        if ((injector as any).scopes?.has('root')) {
          return factories;
        }
        factories.forEach((factory: T | ExtensionFactory<T>) => {
          if (!(factory as ExtensionFactory<T>).get && (factory as any).injector !== null) {
            if (type === InjectionType.ROUTE) {
              (factory as any)._injector = injector;
            } else {
              (factory as any).injector = injector;
            }
          }
        });
        return factories;
      })
    );
}

export function fromFactories<T>(
  factories?: Array<
    | T
    | T[]
    | ExtensionFactory<T>
    | ExtensionFactory<T>[]
    | (() => T | ExtensionFactory<T> | Array<T | ExtensionFactory<T>>)
  >,
  router?: Router,
  withFirstEmpty = true
): Observable<T[]> {
  return !Array.isArray(factories) || factories.length < 1
    ? of([])
    : defer(() => {
        const factoryObservables: Observable<T[]>[] = resolveInjectedFactories(factories).map(f => {
          if (Array.isArray(f)) {
            return toObservableOfArrays(f, withFirstEmpty);
          }
          if (isExtensionFactory(f)) {
            return toObservableOfArrays(f.get(getActivatedRoute(router)), withFirstEmpty);
          }

          return toObservableOfArrays([f], withFirstEmpty);
        });
        return combineLatest(factoryObservables);
      }).pipe(
        map(results => sortByPriority([].concat(...results))),
        map(value => uniq(value))
      );
}

export function resolveInjectedFactories<T>(
  factories: Array<
    | T
    | T[]
    | ExtensionFactory<T>
    | ExtensionFactory<T>[]
    | (() => T | ExtensionFactory<T> | Array<T | ExtensionFactory<T>>)
  >
): Array<T | T[] | ExtensionFactory<T>> {
  return flatten(
    factories.map(f => {
      if (typeof f === 'function') {
        const func = f as () => T | T[] | ExtensionFactory<T>;
        return func();
      }
      return [f];
    })
  );
}

export function stateToFactory<T>(componentsState): ExtensionFactory<T> {
  const components$ = componentsState.pipe(map((componentSet: Set<T>) => [...componentSet]));
  return { get: () => components$ };
}

export function sortByPriority<T>(items: T[]): T[] {
  return sortBy(items, item => -(item?.priority || 0));
}

export function removeDuplicatesIds<T extends { id?: string; priority?: number }>(items: T[]): T[] {
  const grouped: { [key: string]: T[] } = groupBy(items, 'id');
  const itemsWithoutDuplicates = new Array<T>();
  for (const key of Object.keys(grouped)) {
    if (key && key !== 'undefined') {
      const sortedByPrio = sortByPriority(grouped[key]);
      itemsWithoutDuplicates.push(sortedByPrio[0]);
    } else {
      itemsWithoutDuplicates.push(...grouped[key]);
    }
  }
  return sortByPriority(itemsWithoutDuplicates);
}

export function toObservableOfArrays<T>(
  factoryResult: T | T[] | Promise<T | T[]> | Observable<T | T[]>,
  withFirstEmpty: boolean
): Observable<T[]> {
  let observable: Observable<T | T[]>;
  if (!factoryResult) {
    return of([]);
  } else {
    observable = toObservable(factoryResult);
    if (withFirstEmpty) {
      const withEmptyFirst = observable.pipe(startWith([]));
      observable = race(observable, withEmptyFirst);
    }
  }
  return observable.pipe(
    map(result => (Array.isArray(result) ? result : [result]).filter(item => !!item))
  );
}

export function isPromise<T = any>(obj: any): obj is Promise<T> {
  return !!obj && typeof obj.then === 'function';
}

export function isExtensionFactory<T = any>(obj: any): obj is ExtensionFactory<T> {
  return !!obj && typeof obj.get === 'function';
}

/**
 * Converts any value provided to an Observable that emits this value once and then completes.
 * A convenience method to represent all the data as Observables rather than
 * a mixture of Observables and other types.
 *
 * @param value The value the resulting Observable will emit.
 */
export function toObservable<T>(value: T | Promise<T> | Observable<T>): Observable<T> {
  if (isObservable(value)) {
    return value;
  }

  if (isPromise(value)) {
    return from(value as Promise<T>);
  }

  return of(value as T);
}

/**
 * Allows to extend the existing applications from a module.
 */
export interface ExtensionFactory<T> {
  /**
   * Allows to resolve the data of an extension point.
   * The return value can be a Promise or Observable
   * (allowing for asynchronous data resolution).
   *
   * @param activatedRoute The current activated route (if possible to resolve).
   */
  get(activatedRoute?: ActivatedRoute): Observable<T[] | T> | Promise<T[] | T> | T[] | T;
}

/**
 * Extension points allow to extend the application from
 * any module
 */
export interface ExtensionPoint<T> {
  /**
   * Observable that emits of array of extensions active at any give time
   */
  readonly items$: Observable<T[]>;
  /**
   * Additional factories that can be added dynamically. (without hook)
   */
  factories: ExtensionFactory<T>[];
  /**
   * Call the extension factories to refresh them.
   */
  refresh();
}

export abstract class ExtensionPointWithoutStateForPlugins<T> implements ExtensionPoint<T> {
  items$: Observable<T[]>;
  factories: ExtensionFactory<T>[] = [];
  readonly refresh$: Observable<void>;
  /**
   * All injectors to search for an extension.
   */
  protected injectors: Injector[];
  private readonly refreshTrigger = new Subject<void>();

  constructor(rootInjector: Injector, pluginService: PluginsResolveService) {
    this.injectors = [rootInjector];
    pluginService.injectors$.subscribe(injector => {
      this.injectors.push(injector);
    });
    this.refresh$ = merge(this.refreshTrigger, pluginService.refresh$);
  }

  /**
   * Refresh the extension factories
   */
  refresh(): void {
    this.refreshTrigger.next();
  }

  /**
   * Should be called within the constructor of the extending class and set the items$ attribute.
   */
  protected abstract setupItemsObservable(): Observable<T[]>;
}

export abstract class ExtensionPointForPlugins<T>
  extends StateService
  implements ExtensionPoint<T>
{
  items$: Observable<T[]>;
  factories: ExtensionFactory<T>[] = [];
  readonly refresh$: Observable<void>;
  readonly state$ = new BehaviorSubject<Set<T>>(new Set<T>());
  /**
   * All injectors to search for an extension.
   */
  protected injectors: Injector[];
  private readonly refreshTrigger = new Subject<void>();

  constructor(rootInjector: Injector, pluginService: PluginsResolveService) {
    super();
    this.injectors = [rootInjector];
    pluginService.injectors$.subscribe(injector => {
      this.injectors.push(injector);
    });
    this.refresh$ = merge(this.refreshTrigger, pluginService.refresh$);
  }

  /**
   * Refresh the extension factories
   */
  refresh(): void {
    this.refreshTrigger.next();
  }

  /**
   * Should be called within the constructor of the extending class and set the items$ attribute.
   */
  protected abstract setupItemsObservable(): Observable<T[]>;
}

/**
 * Helper function to get the activated route in
 * a service (as ActivatedRoute injection only
 * works in components). Works as long as we only use
 * a tree and no child is active at the same time.
 *
 * @param router The current router
 */
export function getActivatedRoute(router: Router): ActivatedRoute {
  if (router && router.routerState && router.routerState.root) {
    let route = router.routerState.root;
    while (route.firstChild) {
      route = route.firstChild;
    }
    return route;
  }
}

export type GenericHookType<T> = T | T[] | Type<ExtensionFactory<T>>;
export type HookValueType<T> = T | T[] | Type<T>;

/**
 * A generic function to be used by specific implementations of the HOOK concept.
 * @param items The items that should be provided under the `useValue` or `useClass` attribute.
 * Allows an extension factory to be passed as an argument, which can create instances of type T.
 * @param token The InjectionToken/HOOK to be provided.
 * @param options If this is a multi provider or not (defaults to true) and provider type definition (defaults to ClassProvider) - `HookOptions`.
 * @returns A `Provider` (either `ValueProvider` or `ClassProvider`) to be provided in a module.
 */
export function hookGeneric<T>(
  items: GenericHookType<T> | HookValueType<T>,
  token: InjectionToken<T>,
  options?: Partial<HookOptions>
): ValueProvider | ClassProvider | ExistingProvider {
  const finalOptions: HookOptions = Object.assign(
    { multi: true, providerType: HookProviderTypes.ClassProvider },
    options
  );
  const { multi, providerType } = finalOptions;
  if (typeof items !== 'function') {
    return {
      provide: token,
      useValue: items,
      multi
    } as ValueProvider;
  }

  if (providerType === HookProviderTypes.ExistingProvider) {
    return {
      provide: token,
      useExisting: items,
      multi
    } as ExistingProvider;
  }

  return {
    provide: token,
    useClass: items,
    multi
  } as ClassProvider;
}
export interface HookOptions {
  multi: boolean;
  providerType: HookProviderTypes;
}

export enum HookProviderTypes {
  ExistingProvider = 'ExistingProvider',
  ClassProvider = 'ClassProvider'
}

export function allEntriesAreEqual(previous: Array<unknown>, next: Array<unknown>): boolean {
  if (previous === next) return true;
  if (previous == null || next == null) return false;
  if (previous.length !== next.length) return false;

  for (let i = 0; i < previous.length; ++i) {
    if (previous[i] !== next[i]) return false;
  }
  return true;
}
