import { ApplicationRemotePlugins, VersioningMatrix } from '@c8y/client';
export interface PluginsConfig {
  remotes: ApplicationRemotePlugins;
}

export enum ApplicationPluginStatus {
  ORPHANED = 'ORPHANED',
  LATEST = 'LATEST',
  OUTDATED = 'OUTDATED',
  REVOKED = 'REVOKED'
}

export enum PackageType {
  /**
   * A package coming from the official Software AG github enterprise.
   */
  OFFICIAL = 'OFFICIAL',
  /**
   * A package coming from any connected partner repository.
   */
  COMMUNITY = 'COMMUNITY',
  /**
   * A package where the origin is unknown (e.g. uploaded to management with an unknown label)
   */
  UNKNOWN = 'UNKNOWN',
  /**
   * A custom package e.g. uploaded to the tenant by a user
   */
  CUSTOM = 'CUSTOM'
}

export interface ApplicationPlugin {
  id: string;
  name?: string;
  module: string;
  path: string;
  description?: string;
  version?: string;
  scope?: string;
  installed?: boolean;
  contextPath?: string;
  status?: ApplicationPluginStatus;
  tags?: string[];
  license?: string;
  type?: PackageType;
  versioningMatrix?: VersioningMatrix;
}
