import { Component, EnvironmentInjector, Injector, Input, ViewChild } from '@angular/core';
import { Action, ActionWithTemplate } from './action.model';
import { ActionService } from './action.service';

/**
 * A <c8y-action> can be added with content projection.
 *
 * @example
 * ```html
 * <c8y-action>
 *   <button class="btn" (click)="myAction()">My action</button>
 * </c8y-action>
 * ```
 */
@Component({
  selector: 'c8y-action',
  templateUrl: './action.component.html'
})
export class ActionComponent implements ActionWithTemplate {
  /**
   * Is this action disabled.
   */
  @Input() disabled = false;
  /**
   * The priority of this action (the higher the number, the higher the action will be displayed; optional)
   */
  @Input() priority = 0;
  /**
   * The icon to show on this action
   */
  @Input() icon: string;

  @ViewChild('template', { static: false }) template: any;

  @Input() injector: Injector;

  private viewInitTimeout;

  constructor(private actionService: ActionService, _injector: EnvironmentInjector) {
    this.injector = _injector;
  }

  /**
   * After page view is initialized, component adds itself to global action list.
   */
  ngAfterViewInit() {
    this.viewInitTimeout = setTimeout(() => this.actionService.add(this as Action));
  }

  /**
   * Action is removed from global action list, if component is not used anymore.
   */
  ngOnDestroy() {
    clearTimeout(this.viewInitTimeout);
    this.actionService.remove(this as Action);
  }
}
