import { Injectable, Injector } from '@angular/core';
import { set } from 'lodash';
import { Observable, Subject } from 'rxjs';
import { DynamicComponentDefinition } from './dynamic-component.model';
import { DynamicDetailsResolver } from './dynamic-details-resolver';

@Injectable({ providedIn: 'root' })
export class DynamicResolverService {
  bulkResolvingTrigger$: Observable<number>;
  private _triggerBulkResolving = new Subject<number>();
  private requestId = 0;

  constructor(private injector: Injector) {
    this.bulkResolvingTrigger$ = this._triggerBulkResolving.asObservable();
  }

  async executeResolvers(
    details: Array<{ componentId: string; config: any }>,
    dynamicDef: DynamicComponentDefinition[]
  ): Promise<unknown[]> {
    const requestId = this.getRequestId();
    const res = dynamicDef.map((def, index) =>
      this.executeResolversForSingleComponent<any>(def, details[index].config, requestId)
    );

    const promise = this.waitForResults(res);
    this.triggerResolving(requestId);

    return await promise;
  }

  serialize(
    details: Array<{ componentId: string; config: any }>,
    dynamicDef: DynamicComponentDefinition[]
  ) {
    return dynamicDef.map((def, index) => {
      return this.serializeSingleComponent<any>(def, details[index].config);
    });
  }

  private serializeSingleComponent<T>(dynamicDef: DynamicComponentDefinition, config: any) {
    const res: { [key: string]: Partial<T> | Array<Partial<T>> } = {};
    if (!dynamicDef?.resolve) {
      return res;
    }
    Object.entries(dynamicDef.resolve).forEach(([key, value]) => {
      try {
        const resolver = this.injector.get<DynamicDetailsResolver<T>>(value, null);
        if (resolver && resolver.serialize) {
          res[key] = resolver.serialize(config, key);
        } else {
          console.warn(
            `DynamicDetailsResolver: "${value}" not found or does not implement serialize method.`
          );
        }
      } catch {
        console.warn(`Failed to serialize key: "${key}" for dynamic component: "${dynamicDef.id}"`);
      }
    });
    return res;
  }

  private triggerResolving(requestId: number) {
    this._triggerBulkResolving.next(requestId);
  }

  private executeResolversForSingleComponent<T>(
    dynamicDef: DynamicComponentDefinition,
    config: any,
    requestId: number
  ) {
    const res: { [key: string]: T | Promise<T> | Observable<T> } = {};
    if (!dynamicDef?.resolve) {
      return res;
    }
    Object.entries(dynamicDef.resolve).forEach(([key, value]) => {
      try {
        const resolver = this.injector.get<DynamicDetailsResolver<any>>(value, null);
        if (resolver && resolver.resolve) {
          res[key] = resolver.resolve(config, key, requestId);
        } else {
          console.warn(
            `DynamicDetailsResolver: "${value}" not found or does not implement resolve method.`
          );
        }
      } catch {
        console.warn(`Failed to resolve key: "${key}" for dynamic component: "${dynamicDef.id}"`);
      }
    });
    return res;
  }

  private async waitForResults(
    data: Array<{ [key: string]: unknown | Promise<unknown> | Observable<unknown> }>
  ): Promise<any[]> {
    return Promise.all(data.map(tmp => this.waitForResultsOfSingleEntry(tmp)));
  }

  private async waitForResultsOfSingleEntry(data: {
    [key: string]: unknown | Promise<unknown> | Observable<unknown>;
  }): Promise<any> {
    const res = {};
    try {
      const arr = await Promise.all(Object.values(data).map(tmp => this.awaitResult(tmp)));
      Object.keys(data).forEach((key, index) => set(res, key, arr[index]));
    } catch {
      console.warn(`Failed to to resolve data using dynamic component resolver.`);
    }

    return res;
  }

  private async awaitResult(
    data: unknown | Promise<unknown> | Observable<unknown>
  ): Promise<unknown> {
    try {
      if (data instanceof Promise) {
        return await data;
      }
      if (data instanceof Observable) {
        return await data.toPromise();
      }

      return data;
    } catch {
      console.warn(`Failed to to resolve data using dynamic component resolver.`);
      return;
    }
  }

  private getRequestId(): number {
    return this.requestId++;
  }
}
