import { Injector, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { distinctUntilChanged, map, shareReplay } from 'rxjs/operators';
import {
  allEntriesAreEqual,
  ExtensionFactory,
  ExtensionPoint,
  fromTriggerOnce,
  getInjectedHooks,
  removeDuplicatesIds,
  StateService,
  stateToFactory
} from '../common';
import { DrawerItem } from './drawer.model';
import { InjectionToken } from '@angular/core';
import { GenericHookType, hookGeneric, HookOptions } from '../common';

/**
 * A hook to use for Multi Provider extension.
 * Consider using the `hookDrawer` function instead.
 */
const HOOK_DRAWER = new InjectionToken<DrawerItem[]>('HOOK_DRAWER');

/**
 * You can either provide a single `DrawerItem` as parameter:
 * ```typescript
 *  hookDrawer(...)
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  hookDrawer([...])
 * ```
 *
 * Or you provide a Service that implements `ExtensionFactory<DrawerItem>`
 * ```typescript
 *  export class MyDrawerFactory implements ExtensionFactory<DrawerItem> {...}
 *  ...
 *  hookDrawer(MyDrawerFactory)
 * ```
 * A typed alternative to `HOOK_DRAWER`.
 * @param drawer The `DrawerItem`'s or `ExtensionFactory` to be provided.
 * @returns An `Provider` to be provided in your module.
 */
export function hookDrawer(drawer: GenericHookType<DrawerItem>, options?: Partial<HookOptions>) {
  return hookGeneric<DrawerItem>(drawer, HOOK_DRAWER, options);
}

@Injectable({ providedIn: 'root' })
export class DrawerService extends StateService implements ExtensionPoint<DrawerItem> {
  items$: Observable<DrawerItem[]>;

  /**
   * Refresh the extension factories subject.
   * @readonly
   */
  private readonly refreshTrigger = new Subject<void>();
  readonly state$ = new BehaviorSubject<Set<DrawerItem>>(new Set<DrawerItem>());

  /**
   * Additional factories that can be added by plugins.
   */
  factories: ExtensionFactory<DrawerItem>[] = [];
  injectors = [this.rootInjector];

  constructor(private rootInjector: Injector, private router: Router) {
    super();

    this.items$ = fromTriggerOnce<DrawerItem>(router, this.refreshTrigger, [
      getInjectedHooks<DrawerItem>(HOOK_DRAWER, this.injectors),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      distinctUntilChanged(allEntriesAreEqual),
      map((drawerItems: DrawerItem[]) => removeDuplicatesIds(drawerItems)),
      shareReplay(1)
    );
  }

  /**
   * Returns the current state.
   * @readonly
   * @returns The current set of actions.
   */
  get state(): Set<DrawerItem> {
    return this.state$.value;
  }

  getDrawersForPosition$(position: 'right' | 'left'): Observable<DrawerItem[]> {
    return this.items$.pipe(map(items => items.filter(drawer => drawer.position === position)));
  }

  /**
   * Refresh the extension factories.
   */
  refresh() {
    this.refreshTrigger.next();
  }

  /**
   * Adds a new node to the navigator.
   * @param {DrawerItem} node Navigator node to add.
   */
  add(node: DrawerItem) {
    this.state.add(node);
    this.emitNewState();
  }

  /**
   * Removes a node from the navigator.
   * @param {DrawerItem} node Navigator node to remove.
   */
  remove(node: DrawerItem) {
    this.state.delete(node);
    this.emitNewState();
  }
}
