import { Injectable } from '@angular/core';
import { isEmpty, isNil, omitBy } from 'lodash-es';
import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, map } from 'rxjs/operators';
import { UserPreferencesService } from '../common/user-preferences/user-preferences.service';
import { GridConfigContext } from './data-grid-configuration.model';
import { Column, CustomColumnConfig, GridConfig } from './data-grid.model';
import { DataGridService } from './data-grid.service';

interface LegacyColumnConfig {
  key: string;
  headerName: string;
  active: boolean;
  custom: boolean;
  fragmentPath: string;
}

interface LegacyColumnFilterConfig {
  [key: string]: {
    filtering: object;
    sorting: {
      order: number;
    };
  };
}

@Injectable({
  providedIn: 'root'
})
export class LegacyGridConfigMapperService {
  static deviceGridLegacyKeyToName = {
    status: 'status',
    name: 'name',
    model: 'model',
    serialNumber: 'serialNumber',
    group: 'group',
    registrationDate: 'registrationDate',
    systemId: 'systemId',
    imei: 'imei',
    alarms: 'alarms'
  };

  constructor(
    private userPreferencesService: UserPreferencesService,
    private dataGridService: DataGridService
  ) {}

  getMappedGridConfig(context: GridConfigContext): Observable<GridConfig> {
    const legacyAllDevicesGridKey = context.legacyConfigKey;
    const legacyAllDevicesGridFilterKey = context.legacyFilterKey;
    return combineLatest([
      this.userPreferencesService.get(legacyAllDevicesGridKey),
      this.userPreferencesService.get(legacyAllDevicesGridFilterKey)
    ]).pipe(
      map(([legacyConfig, legacyFilterConfig]) =>
        this.mapLegacyToDeviceGridConfig(legacyConfig, legacyFilterConfig, context.defaultColumns)
      ),
      concatMap(mappedLegacyConfig => {
        if (mappedLegacyConfig) {
          return Promise.all([
            this.userPreferencesService.set(legacyAllDevicesGridKey, null),
            this.userPreferencesService.set(legacyAllDevicesGridFilterKey, null)
          ]).then(() => mappedLegacyConfig);
        } else {
          return of(null);
        }
      }),
      concatMap(mappedLegacyConfig =>
        mappedLegacyConfig
          ? this.userPreferencesService
              .set(context?.key, mappedLegacyConfig)
              .then(() => mappedLegacyConfig)
          : this.userPreferencesService.get(context?.key)
      ),
      map(
        config =>
          config || {
            columns: [],
            pagination: {
              pageSize: (this.dataGridService as any).DEFAULT_PAGE_SIZE,
              currentPage: 1
            }
          }
      )
    );
  }

  mapLegacyToDeviceGridConfig(
    legacyConfig: LegacyColumnConfig[],
    legacyFilterConfig: LegacyColumnFilterConfig,
    defaultColumns: Column[] = []
  ): GridConfig {
    if (Array.isArray(legacyConfig) || !isEmpty(legacyFilterConfig)) {
      return {
        columns: this.getConfigColumns(legacyConfig, legacyFilterConfig, defaultColumns),
        pagination: {
          pageSize: (this.dataGridService as any).DEFAULT_PAGE_SIZE,
          currentPage: 1
        }
      };
    }
  }

  getConfigColumns(
    legacyConfig: LegacyColumnConfig[],
    legacyFilterConfig: LegacyColumnFilterConfig,
    defaultColumns: Column[]
  ) {
    const legacyFilterConfigArray = legacyFilterConfig
      ? Object.keys(legacyFilterConfig).map(key => ({
          key,
          filter: {
            externalFilterQuery: legacyFilterConfig[key].filtering
          },
          sorting: legacyFilterConfig[key].sorting
        }))
      : [];

    const config = this.mergeLegacyConfigs(
      legacyConfig || defaultColumns.map(column => ({ key: column.name })),
      legacyFilterConfigArray
    );

    return config
      .filter(column => column.key !== 'removalColumn')
      .map(this.mapLegacyColumnConfig.bind(this));
  }

  mergeLegacyConfigs(columnConfig, filterConfig) {
    return columnConfig.map(column => ({
      ...filterConfig.find(item => item.key === column.key),
      ...column
    }));
  }

  mapLegacyColumnConfig(legacy): CustomColumnConfig {
    const { active, key, custom, headerName, fragmentPath, filter, sorting } = legacy;
    const sortOrder = sorting ? this.migrateSortOrder(sorting) : '';

    return omitBy(
      {
        visible: active ?? true,
        name: LegacyGridConfigMapperService.deviceGridLegacyKeyToName[key] || key,
        sortOrder,
        custom,
        header: custom ? headerName : null,
        path: fragmentPath,
        filter
      },
      isNil
    );
  }

  migrateSortOrder(sorting) {
    switch (sorting.order) {
      case 0:
        return '';
      case -1:
        return 'desc';
      case 1:
        return 'asc';
    }
  }
}
