import { Pipe, PipeTransform } from '@angular/core';
import { IUser } from '@c8y/client';
import { TranslateService } from '@ngx-translate/core';

@Pipe({
  name: 'userRolesList'
})
export class UserRolesListPipe implements PipeTransform {
  constructor(private translateService: TranslateService) {}
  transform(user: IUser, translate = true): string {
    if (!user) {
      return '';
    }

    const groups: Array<any> = (user as any).groups?.references || [];
    let groupNames = groups.map(ref => ref.group?.name).filter(groupName => !!groupName);

    if (!groupNames.length) {
      return '';
    }

    if (translate) {
      groupNames = groupNames.map(name => this.translateService.instant(name));
    }

    return groupNames.join(', ');
  }
}
