import { Injectable } from '@angular/core';
import { ExtensionFactory, OptionsService } from '../common';
import { DrawerItem } from '../drawer';
import { VersionListComponent } from './version-list/version-list.component';

@Injectable()
export class VersionDrawerFactory implements ExtensionFactory<DrawerItem> {
  protected drawerItem: DrawerItem = {
    component: VersionListComponent,
    position: 'right',
    priority: 80,
    id: 'platformInformation'
  };

  constructor(private options: OptionsService) {}

  get(): DrawerItem[] | DrawerItem {
    if (this.shouldShowPlatformInformation()) {
      return this.drawerItem;
    }
    return [];
  }

  private shouldShowPlatformInformation(): boolean {
    const hidePlatformInformation = this.options.get('hidePlatformInformation');
    if (hidePlatformInformation === undefined) {
      return !this.options.get('hidePowered');
    }

    return !hidePlatformInformation;
  }
}
