import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnDestroy,
  OnInit
} from '@angular/core';
import { DismissAlertStrategy } from './dynamic-component-alert.model';
import { DynamicComponentAlertAggregator } from './dynamic-component-alert-aggregator';
import { AlertOutletBase, AlertService } from '../alert';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
  selector: 'c8y-dynamic-component-alerts',
  templateUrl: './dynamic-component-alerts.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DynamicComponentAlertsComponent extends AlertOutletBase implements OnInit, OnDestroy {
  dismissAlertStrategy: typeof DismissAlertStrategy = DismissAlertStrategy;
  private destroy$: Subject<void> = new Subject<void>();

  @Input() alerts: DynamicComponentAlertAggregator;

  constructor(public alertService: AlertService, private ref: ChangeDetectorRef) {
    super(alertService);
  }

  ngOnInit() {
    this.alerts.anyAlertExists$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.ref.markForCheck();
    });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
