import { Injectable, Injector } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { distinctUntilChanged, map, shareReplay } from 'rxjs/operators';
import {
  ExtensionFactory,
  ExtensionPoint,
  fromTriggerOnce,
  getInjectedHooks,
  sortByPriority,
  StateService,
  stateToFactory
} from '../common';
import { CleanedVersion, HOOK_VERSION, Version } from './version.model';
import { pick } from 'lodash-es';

@Injectable({
  providedIn: 'root'
})
export class VersionService extends StateService implements ExtensionPoint<Version> {
  items$: Observable<Version[]>;
  nonHiddenItems$: Observable<Version[]>;
  readonly refreshTrigger = new Subject<void>();
  readonly state$ = new BehaviorSubject<Set<Version>>(new Set<Version>());
  factories: ExtensionFactory<Version>[] = [];
  injectors = [this.rootInjector];

  constructor(private rootInjector: Injector) {
    super();
    this.items$ = fromTriggerOnce<Version>(undefined, this.refreshTrigger, [
      getInjectedHooks<Version>(HOOK_VERSION, this.injectors),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      distinctUntilChanged(),
      map(versions => sortByPriority(versions)),
      shareReplay(1)
    );

    this.nonHiddenItems$ = this.items$.pipe(
      map(versions => versions.filter(version => !version.hidden))
    );
  }

  get state(): Set<Version> {
    return this.state$.value;
  }

  refresh() {
    this.refreshTrigger.next();
  }

  add(version: Version) {
    this.state.add(version);
    this.emitNewState();
  }

  remove(version: Version) {
    this.state.delete(version);
    this.emitNewState();
  }

  cleanUpVersions(versions: Version[]): CleanedVersion[] {
    return versions.map(version => pick(version, 'label', 'version', 'type', 'custom'));
  }
}
